'use strict'

import log from 'common/log'
import argCheck from 'common/argCheck'

import {audioContextUtil, AudioContext} from 'webrtc/utils'

import MediaFilter from 'media/mediaFilter'

const TAG = 'web-audio-filter'

/**
 * A base class for media filter nodes that wish to use the WebAudio APIs.
 *
 * @class WebAudioFilter
 * @extends MediaFilter
 * @param {Object} [options] - Optional options object
 * @param {AudioContext} [options.context] - Audio context to use within this media node,
 *   which must not be closed as while the filter is still being used.
 *   If omitted, an internal context will be used.
 */
export default class WebAudioFilter extends MediaFilter {
  constructor(options = {}) {
    super()
    argCheck.optOptions('WebAudioFilter constructor', 'options', options)
      .optInstance('context', AudioContext)

    if (options.context) {
      this._releaseContext = false
      this._audioContext = options.context
    } else {
      this._releaseContext = true
      this._audioContext = null
    }

    this._source = null
    this._destination = null
  }

  get context() {
    return this._audioContext
  }

  get audioSource() {
    return this._source
  }

  get audioDestination() {
    return this._destination
  }

  onStream(stream, oldStream) {
    if (this._source) {
      this._source.disconnect()
      this._source = null
    }
    if (stream) {
      if (this._releaseContext) {
        this._audioContext = audioContextUtil.get()
      }
      this._source = this._audioContext.createMediaStreamSource(stream)
      this._destination = this._audioContext.createMediaStreamDestination()
      this.setStream(this._destination.stream)
    } else {
      if (this._audioContext && this._releaseContext) {
        audioContextUtil.release()
        this._audioContext = null
      }
      this._destination = null
      this.setStream(null)
    }

    try {
      this.onAudioSource(this._source, this._destination, this._audioContext)
    } catch (error) {
      log.error(TAG, `Error thrown in WebAudioFilter.onAudioSource: ${error}`)
    }
  }

  onAudioSource(source, destination, context) {
    if (source) {
      if (this.onSetupFilter) {
        try {
          let result = this.onSetupFilter(context)
          if (result instanceof AudioNode) {
            source.connect(result)
            result.connect(destination)
          } else if (Array.isArray(result)) {
            let [input, output] = result
            if (!(input instanceof AudioNode)) {
              throw new TypeError('returned input node must be an AudioNode')
            }
            if (!(output instanceof AudioNode)) {
              throw new TypeError('returned output node must be an AudioNode')
            }
            source.connect(input)
            output.connect(destination)
          } else {
            throw new TypeError('return value must be an AudioNode or array of [input, output] nodes')
          }
        } catch (error) {
          log.error(TAG, `Error thrown in WebAudioFilter.onSetupFilter: ${error}`)
        }
      } else {
        log.warning(TAG, 'WebAudioFilter should override onAudioSource(source, destination, context) ' +
          'or implement onSetupFilter(context)')
      }
    } else {
      if (this.onTeardownFilter) {
        try {
          this.onTeardownFilter(context)
        } catch (error) {
          log.error(TAG, `Error thrown in WebAudioFilter.onTeardownFilter: ${error}`)
        }
      }
    }
  }
}



