'use strict'

import argCheck from 'common/argCheck'

import {ConfigurationData} from 'conference/raftConfiguration'
import LogEntry from 'conference/raftLogEntry'
import {PeerTopologyInfo} from 'conference/meshTopology'

export default class RaftMessage {
  static parse(body) {
    if (!Array.isArray(body)) {
      throw new TypeError('RaftMessage body must be an array')
    }
    let [id, ...params] = body
    let MessageConstructor = messageConstructors[id]
    if (!MessageConstructor) {
      throw new TypeError(`Failed to parse invalid raft message, unknown id: ${id}`)
    }
    return new MessageConstructor(...params)
  }

  constructor(term) {
    this._term = argCheck.number(this, 'term', term)
  }

  get term() {
    return this._term
  }

  get isRequest() {
    return !(this.constructor.id & 1)
  }

  toJSON() {
    return [this.constructor.id, ...this.params]
  }
}

export class AppendEntriesRequest extends RaftMessage {
  static create({term, topologyIndex, prevIndex, prevTerm, commitIndex, entries}) {
    return new AppendEntriesRequest(term, topologyIndex, prevIndex, prevTerm, commitIndex, entries)
  }

  constructor(term, topologyIndex, prevIndex, prevTerm, commitIndex, entries) {
    super(term)
    this._topologyIndex = argCheck.number(this, 'topologyIndex', topologyIndex)
    this._prevIndex = argCheck.number(this, 'prevIndex', prevIndex)
    this._prevTerm = argCheck.number(this, 'prevTerm', prevTerm)
    this._commitIndex = argCheck.number(this, 'commitIndex', commitIndex)
    this._entries = entries.map((entry, index) => {
      let entryIndex = this._prevIndex + index + 1
      entry = (entry instanceof LogEntry) ? entry : LogEntry.parse(entry, entryIndex)
      argCheck.number(this, 'entry.term', entry.term)
      return entry
    })
  }

  toString() {
    return 'appendRequest{' +
      `term=${this.term},ti=${this._topologyIndex},` +
      `pi=${this._prevIndex},pt=${this._prevTerm},` +
      `ci=${this._commitIndex},es=[${this._entries}]}`
  }

  get params() {
    return [
      this._term,
      this._topologyIndex,
      this._prevIndex,
      this._prevTerm,
      this._commitIndex,
      this._entries,
    ]
  }

  // topologyIndex is an addition to the Raft algorithm. The purpose is for the leader
  // to get a complete view of the topology of the cluster, while keeping network use low.
  // Each member will send information about their active connections in the AppendEntries
  // response, but only if the received topology index is lower than their own one.
  //
  // Followers will increment their topologyIndex whenever a significant change is made to
  // their active connections, e.g. added and removed connections, as well as changes in
  // latency or bandwidth. The initial implementation will only take added and removed
  // connections into account, and use a timeout to make sure relatively up-to-date network
  // information is transmitted. The downside is that it will require slightly more bandwidth,
  // and that bit changes in network characteristics won't be detected as quickly by the leader.
  //
  // Just like terms and log indices, the topology index is an integer that is always
  // incremented during normal operation. The follower increments it as discussed above
  // and the leader increments their topologyIndex as updates are received from the follower.
  // Just like the matchIndex the leader keeps track of the remote topologyIndex separately
  // for each cluster member.
  //
  // New leaders will use a topologyIndex of 0 in the first appendEntries request sent to
  // all members when they get elected, and the followers will then always respond with their
  // current connection state and topologyIndex.
  get topologyIndex() {
    return this._topologyIndex
  }

  get prevIndex() {
    return this._prevIndex
  }

  get prevTerm() {
    return this._prevTerm
  }

  get commitIndex() {
    return this._commitIndex
  }

  get entries() {
    return this._entries
  }

  response(options = {}) {
    let {
      term = this._term,
      success = false,
      needSnapshot = false,
      meshTopology,
      matchIndex = this._prevIndex - 1,
    } = options

    let localTopologyInfo = meshTopology && meshTopology.localTopologyInfo
    var topologyInfo = null
    let topologyIndex = localTopologyInfo && localTopologyInfo.topologyIndex
    if (topologyIndex > this.topologyIndex) {
      topologyInfo = localTopologyInfo
    }
    let volume = localTopologyInfo ? localTopologyInfo.volume : 0

    var flag = APPEND_ENTRIES_RESPONSE_FLAG_SUCCESS
    if (!success) {
      flag = APPEND_ENTRIES_RESPONSE_FLAG_FAILURE
    }
    if (needSnapshot) {
      flag = APPEND_ENTRIES_RESPONSE_FLAG_NEED_SNAPSHOT
    }

    return new AppendEntriesResponse(term, flag, topologyIndex, topologyInfo, volume, matchIndex)
  }
}

const APPEND_ENTRIES_RESPONSE_FLAG_SUCCESS = 0
const APPEND_ENTRIES_RESPONSE_FLAG_FAILURE = 1
const APPEND_ENTRIES_RESPONSE_FLAG_NEED_SNAPSHOT = 2

export class AppendEntriesResponse extends RaftMessage {
  constructor(term, flag, topologyIndex = -1, topologyInfo = null, volume = 0, matchIndex = -1) {
    super(term)
    this._flag = argCheck.number(this, 'flag', flag)
    this._topologyIndex = argCheck.number(this, 'topologyIndex', topologyIndex)
    this._matchIndex = argCheck.number(this, 'matchIndex', matchIndex)
    if (topologyInfo) {
      this._topologyInfo = PeerTopologyInfo.parseUpdate(topologyInfo)
    } else {
      this._topologyInfo = null
    }
    this._volume = volume
  }

  toString() {
    var topologyInfo = null
    if (this._topologyInfo) {
      topologyInfo = `topologyInfo{power=${this._topologyInfo.power},${this._topologyInfo.links}}`
    }
    return 'appendResponse{' +
      `term=${this.term},flag=${this._flag},mi=${this._matchIndex},v=${this._volume},` +
      `ti=${this._topologyIndex},${topologyInfo}}`
  }

  get params() {
    return [this._term, this._flag, this._topologyIndex, this._topologyInfo || 0, this._volume || 0, this._matchIndex]
  }

  get success() {
    return this._flag === APPEND_ENTRIES_RESPONSE_FLAG_SUCCESS
  }

  get needSnapshot() {
    return this._flag === APPEND_ENTRIES_RESPONSE_FLAG_NEED_SNAPSHOT
  }

  get topologyIndex() {
    return this._topologyIndex
  }

  get topologyInfo() {
    return this._topologyInfo
  }

  get matchIndex() {
    return this._matchIndex
  }

  get volume() {
    return this._volume
  }
}

export class RequestVoteRequest extends RaftMessage {
  static create({term, lastLogTerm, lastLogIndex}) {
    return new RequestVoteRequest(term, lastLogTerm, lastLogIndex)
  }

  constructor(term, lastLogTerm, lastLogIndex) {
    super(term)
    this._lastLogTerm = argCheck.number(this, 'lastLogTerm', lastLogTerm)
    this._lastLogIndex = argCheck.number(this, 'lastLogIndex', lastLogIndex)
  }

  toString() {
    return `voteRequest{term=${this.term}}`
  }

  get params() {
    return [this._term, this._lastLogTerm, this._lastLogIndex]
  }

  get lastLogTerm() {
    return this._lastLogTerm
  }

  get lastLogIndex() {
    return this._lastLogIndex
  }

  compareLog(other) {
    let {lastLogTerm} = other
    if (this.lastLogTerm !== lastLogTerm) {
      return this.lastLogTerm - lastLogTerm
    } else {
      return this.lastLogIndex - other.lastLogIndex
    }
  }

  response({granted = false}) {
    return new RequestVoteResponse(this._term, granted)
  }
}

export class RequestVoteResponse extends RaftMessage {
  constructor(term, granted) {
    super(term)
    this._granted = argCheck.boolean(this, 'granted', granted)
  }

  toString() {
    return `voteResponse{term=${this.term},granted=${this.granted}}`
  }

  get params() {
    return [this._term, this._granted]
  }

  get granted() {
    return this._granted
  }
}

// PreVote RPC is an extension for the Raft algorithm that lets low-population
// clusters find a leader faster, and prevents many cases of leader flip-flopping.
//
// The implementation is based on the suggested modifications in
// "Three modifications for the Raft consensus algorithm" by Henrik Ingo
// http://openlife.cc/system/files/3-modifications-for-Raft-consensus.pdf
export class PreVoteRequest extends RequestVoteRequest {
  static create({term, lastLogTerm, lastLogIndex}) {
    return new PreVoteRequest(term, lastLogTerm, lastLogIndex)
  }

  toString() {
    return `preVoteRequest{term=${this.term}}`
  }

  response({granted = false}) {
    return new PreVoteResponse(this._term, granted)
  }
}

export class PreVoteResponse extends RequestVoteResponse {
  toString() {
    return `preVoteResponse{term=${this.term},granted=${this.granted}}`
  }
}

export class SnapshotRequest extends RaftMessage {
  static create({term, lastTerm, lastIndex, configuration, state}) {
    return new SnapshotRequest(term, lastTerm, lastIndex, configuration, state)
  }

  constructor(term, lastTerm, lastIndex, configuration, state) {
    super(term)
    this._lastTerm = argCheck.number(this, 'lastTerm', lastTerm)
    this._lastIndex = argCheck.number(this, 'lastIndex', lastIndex)
    if (!(configuration instanceof ConfigurationData)) {
      configuration = ConfigurationData.parse(configuration)
    }
    this._configuration = configuration
    this._state = state
  }

  toString() {
    return `snapshotRequest{term=${this.term},li=${this.lastIndex},lt=${this.lastTerm}` +
      `,${this.configuration},${JSON.stringify(this.state)}}`
  }

  get params() {
    return [this._term, this._lastTerm, this._lastIndex, this._configuration, this._state]
  }

  get lastTerm() {
    return this._lastTerm
  }

  get lastIndex() {
    return this._lastIndex
  }

  get configuration() {
    return this._configuration
  }

  get state() {
    return this._state
  }

  response() {
    return new SnapshotResponse(this._term, this._lastTerm, this._lastIndex)
  }
}

export class SnapshotResponse extends RaftMessage {
  constructor(term, lastTerm, lastIndex) {
    super(term)
    this._lastTerm = argCheck.number(this, 'lastTerm', lastTerm)
    this._lastIndex = argCheck.number(this, 'lastIndex', lastIndex)
  }

  toString() {
    return `snapshotResponse{term=${this.term},li=${this.lastIndex},lt=${this.lastTerm}}`
  }

  get params() {
    return [this._term, this._lastTerm, this._lastIndex]
  }

  get lastTerm() {
    return this._lastTerm
  }

  get lastIndex() {
    return this._lastIndex
  }
}

export class StateOpRequest extends RaftMessage {
  static create({id, op, args}) {
    return new StateOpRequest(id, op, args)
  }

  constructor(id, op, args) {
    super(0)
    this._id = id
    this._op = op
    this._args = args
  }

  toString() {
    return `stateOpRequest{id=${this._id},op=${this._op},args=${this._args}}`
  }

  get id() {
    return this._id
  }

  get op() {
    return this._op
  }

  get args() {
    return this._args
  }

  get params() {
    return [this._id, this._op, this._args]
  }

  response({errorCode = 0} = {}) {
    return new StateOpResponse(this._id, errorCode)
  }
}

export class StateOpResponse extends RaftMessage {
  constructor(id, errorCode) {
    super(0)
    this._id = id
    this._errorCode = errorCode
  }

  toString() {
    return `stateOpResponse{id=${this._id},errorCode=${this._errorCode}}`
  }

  get id() {
    return this._id
  }

  get errorCode() {
    return this._errorCode
  }

  get params() {
    return [this._id, this._errorCode]
  }
}
StateOpResponse.NOT_LEADER = 1

AppendEntriesRequest.id = 2
AppendEntriesResponse.id = 3
RequestVoteRequest.id = 4
RequestVoteResponse.id = 5
PreVoteRequest.id = 6
PreVoteResponse.id = 7
SnapshotRequest.id = 8
SnapshotResponse.id = 9
StateOpRequest.id = 10
StateOpResponse.id = 11

const messageConstructors = {
  2: AppendEntriesRequest,
  3: AppendEntriesResponse,
  4: RequestVoteRequest,
  5: RequestVoteResponse,
  6: PreVoteRequest,
  7: PreVoteResponse,
  8: SnapshotRequest,
  9: SnapshotResponse,
  10: StateOpRequest,
  11: StateOpResponse,
}



