'use strict'

import log from 'common/log'
import argCheck from 'common/argCheck'
import {isObject, priv} from 'common/utils'
import {toMatrixIdForServer} from 'matrix/ids'
import User from 'matrix/user'

export default function UserRegistry() {
  this._dependencies = null
  this._users = {}
}

UserRegistry.prototype = {
  constructor: UserRegistry,
  tag: 'user-registry',
  toString: function () {
    return 'userRegistry{}'
  },
  getUser: function getUser(arg) {
    let matrixId = toUserId('UserRegistry.get', arg, this._dependencies.serverName)
    let user = this._users[matrixId]
    if (!user) {
      user = priv(User, {
        matrixId,
        serverName: this._dependencies.serverName,
      }, this._dependencies.imageResourceFactory)
      this._users[matrixId] = user
    }
    return user
  },

  // dependencies.ownUserId
  // dependencies.imageResourceFactory
  init: function (dependencies) {
    this._dependencies = dependencies
    return this
  },
  clear: function () {
    this._dependencies = null
    this._users = {}
  },

  _handlePresenceSync: function (presenceEvents) {
    for (var i in presenceEvents) {
      var event = presenceEvents[i]
      var content = event.content
      var matrixId = event.sender
      var user = this.getUser(matrixId)
      user._update(content)
      log.verbose('events', `got presence event for user ${matrixId}`, user)
    }
  },
}

export function toUserId(context, user, serverName) {
  var id
  if (user instanceof User) {
    id = user._matrixId
  } else if (isObject(user)) {
    id = user.user_id || user.userId || user.id
  } else {
    id = user
  }
  argCheck.string(context, 'id', id)
  return toMatrixIdForServer(context, id, serverName, 'user')
}



