'use strict'

import log from 'common/log'
import CctError from 'common/errors'
import {
  assign,
  defer,
  map,
  shallowCopy,
} from 'common/utils'

import EmitterMixin from 'common/emitter'

export class RequestError extends CctError {
  constructor(message, xhrStatus) {
    super(message)
    this.xhrStatus = xhrStatus
  }
}
RequestError.errorName = 'RequestError'

export class ResponseError extends CctError {}
ResponseError.errorName = 'ResponseError'

export class JsonResponse {}

export default function Request(copy) {
  if (!this) {
    return new Request()
  }
  copy = copy || {}
  this._origin = copy._origin || ''
  this._path = copy._path || []
  this._params = copy._params || {}
  this._query = copy._query || {}
  this._config = copy._config || {}
  this._headers = copy._headers || {}
  this._client = copy._client || Request.DEFAULT_CLIENT
  this._errorHandler = copy._errorHandler
  this._fragment = copy._fragment || ''
}

Request.prototype = {
  constructor: Request,
  tag: 'request',

  toString: function () {
    var origin = this._origin
    var path = 'path=' + this._path.join('/')
    var params = 'params=[' + map(this._params, function (value, key) {
      return key + '=' + value
    }).join(',') + ']'
    var query = Object.keys(this._query)

    return 'request{' + [origin, path, params, query].join(',') + '}'
  },

  path: function () {
    var copy = new Request(this)
    copy._path = copy._path.concat.apply(copy._path, arguments)
    return copy
  },
  origin: function (origin) {
    var copy = new Request(this)
    copy._origin = origin
    return copy
  },
  client: function (client) {
    var copy = new Request(this)
    copy._client = client
    return copy
  },
  errorHandler: function (errorHandler) {
    var copy = new Request(this)
    copy._errorHandler = errorHandler
    return copy
  },
  fragment: function (fragment) {
    var copy = new Request(this)
    copy._fragment = fragment
    return copy
  },

  // The below methods append values to each respective object prepresentation
  // If an object is passed as the first argument, all key-value pairs are copied,
  // If a string is passed as first argument, it will be used as key while the second
  // argument will be used as value.
  // e.g. req.headers({'x-ample': 'text'}) or req.headers('x-ample', 'test')

  // will be serialized to a query string when sendings a request
  query: function (obj, value) {
    var copy = new Request(this)
    var query = shallowCopy(this._query)
    if (typeof(obj) === 'string') {
      query[obj] = value
    } else {
      assign(query, obj)
    }
    copy._query = query
    return copy
  },
  // substitutes any key that is found within braces in the url with the value of the param
  // e.g. {userId: 'test'} will change /users/{userId}/ to /users/test/
  params: function (obj, value) {
    var copy = new Request(this)
    var params = shallowCopy(this._params)
    if (typeof(obj) === 'string') {
      params[obj] = value
    } else {
      assign(params, obj)
    }
    copy._params = params
    return copy
  },
  // HTTP headers...
  header: function (obj, value) {
    var copy = new Request(this)
    var headers = shallowCopy(this._headers)
    if (typeof(obj) === 'string') {
      headers[obj] = value
    } else {
      assign(headers, obj)
    }
    copy._headers = headers
    return copy
  },
  config: function (obj, value) {
    var copy = new Request(this)
    var config = shallowCopy(this._config)
    if (typeof(obj) === 'string') {
      config[obj] = value
    } else {
      assign(config, obj)
    }
    copy._config = config
    return copy
  },

  get: function () {
    return this._do('GET', arguments)
  },

  del: function () {
    return this._do('DEL', arguments)
  },

  post: function () {
    return this._do('POST', arguments)
  },

  put: function () {
    return this._do('PUT', arguments)
  },

  _do: function (method, args) {
    var extraPath
    var body = null

    if (method === 'POST' || method === 'PUT') {
      if (args.length === 0) {
        body = {}
      } else if (args.length === 1) {
        body = args[0]
      } else {
        extraPath = args[0]
        body = args[1]
      }
    } else {
      extraPath = args[0]
    }

    var options = this._compileOptions(method, extraPath, body)
    if (this._errorHandler) {
      options.errorHandler = this._errorHandler
    } else {
      options.errorHandler = error => error
    }
    return this._client.executeRequest(options)
  },

  _compileOptions: function (method, extraPath, body) {
    let path = this._path.concat(extraPath || [])
    let params = this._params
    path = path.map(function (component) {
      let match = component.match(/^\{(.+)\}$/)
      if (match && !match[1].match(/^\{.*\}$/)) {
        component = params[match[1]]
      }
      return encodeURIComponent(component)
    }).join('/')
    let url = this._origin + (path ? '/' + path : '')

    let query = this._query
    let queryString = Object.keys(query).filter(function (key) {
      return query[key]
    }).map(function (key) {
      return encodeURIComponent(key) + '=' + encodeURIComponent(query[key])
    }).join('&')

    url += queryString && '?' + queryString
    url += this._fragment && '#' + this._fragment

    return {
      headers: this._headers,
      timeout: this._config.timeout,
      method: method,
      body: body,
      url: url,
    }
  },
}

Request._executeXhrWithJsonResponse = function (xhr, options, body) {
  if (typeof(options.timeout) === 'number') {
    xhr.timeout = options.timeout
  }
  let headers = options.headers
  for (let header in headers) {
    if (headers.hasOwnProperty(header)) {
      xhr.setRequestHeader(header, headers[header])
    }
  }

  function copyResponseInfoTo(res) {
    res.status = xhr.status
    res.statusText = xhr.statusText
    res.url = xhr.responseURL
    res.headers = xhr.getAllResponseHeaders().split('\r\n').reduce(function (obj, line) {
      let index = line.indexOf(': ')
      if (index > 0) {
        obj[line.substring(0, index)] = line.substring(index + 2)
      }
      return obj
    }, {})
  }

  return new Promise((resolve, reject) => {
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== 4) {
        return
      }
      if (!xhr.status) {
        let message = 'failed ' + options.method + ' request to ' + options.url
        reject(new RequestError(message, xhr.status))
        return
      }

      let res = new JsonResponse()
      try {
        if (xhr.responseText) {
          res.body = JSON.parse(xhr.responseText)
        }
      } catch (parseError) {
        let error = new ResponseError(`Failed to parse response JSON: ${parseError.message}`)
        error.reason = parseError
        error.responseText = xhr.responseText
        copyResponseInfoTo(error)
        reject(error)
        return
      }

      copyResponseInfoTo(res)

      if (res.status >= 400) {
        reject(options.errorHandler(res))
      } else {
        resolve(res)
      }
    }
    xhr.send(body || options.body)
  })
}

Request.jsonRequest = function (options) {
  var xhr = new XMLHttpRequest()
  xhr.open(options.method, options.url, true)
  if (xhr.overrideMimeType) {
    xhr.overrideMimeType('application/json')
  }

  var body = JSON.stringify(options.body || {})
  xhr.setRequestHeader('Content-Type', 'application/json')

  return Request._executeXhrWithJsonResponse(xhr, options, body)
}

Request.uploadFile = function (options) {
  var xhr = new XMLHttpRequest()
  xhr.open(options.method, options.url, true)

  var upload = new EmitterMixin()

  xhr.upload.addEventListener('progress', function (event) {
    if (event.lengthComputable) {
      upload._emit('progress', event.loaded / event.total)
    }
  }, false)

  xhr.upload.addEventListener('load', function () {
    upload._emit('done')
  }, false)

  upload.promise = Request._executeXhrWithJsonResponse(xhr, options)

  return upload
}

export function FileUploadClient() {
}

FileUploadClient.prototype = {
  constructor: FileUploadClient,
  tag: 'file-upload-client',

  toString: function () {
    return 'fileUploadClient{}'
  },

  executeRequest: function (options) {
    return Request.uploadFile(options)
  },
}

export function JsonRetryClient() {
  this._execute = this._execute.bind(this)
}

JsonRetryClient.prototype = {
  constructor: JsonRetryClient,
  tag: 'json-retry-client',

  toString: function () {
    return 'jsonRetryClient{}'
  },

  executeRequest: function (options) {
    var request = defer()
    request.options = options
    this._execute(request)
    return request.promise
  },

  _execute: function (request) {
    var self = this
    Request.jsonRequest(request.options).then(function (response) {
      request.resolve(response)
    }, function (err) {
      if (err.name === 'RateLimitError') {
        setTimeout(self._execute, err.retryAfterMs, request)
      } else {
        request.reject(err)
      }
    }).catch(function (error) {
      log.error(self.tag, 'error while executing request: ', error)
    })
  },
}

export function SerializedJsonRetryClient() {
  this._queue = []
  this._working = false
}

SerializedJsonRetryClient.prototype = {
  constructor: SerializedJsonRetryClient,
  tag: 'serialized-json-retry-client',

  toString: function () {
    return 'serializedJsonRetryClient{working=' + this._working +
            ',queued=' + this._queue.length + '}'
  },

  executeRequest: function (options) {
    var request = defer()
    request.options = options
    this._queue.push(request)
    this._poll()
    return request.promise
  },

  _poll: function () {
    if (this._working) {
      return
    }

    var request = this._queue.shift()
    if (!request) {
      return
    }

    var self = this
    this._working = true

    Request.jsonRequest(request.options).then(function (response) {
      request.resolve(response)
      self._working = false
      self._poll()
    }, function (err) {
      if (err.name === 'RateLimitError') {
        setTimeout(function () {
          self._queue.unshift(request)
          self._working = false
          self._poll()
        }, err.retryAfterMs)
      } else {
        request.reject(err)
        self._working = false
        self._poll()
      }
    }).catch(function (error) {
      log.error(self.tag, 'error while executing request: ', error)
    })
  },
}

Request.DEFAULT_CLIENT = new JsonRetryClient()



