'use strict'

import log from 'common/log'

const TAG = 'candidates'
const CANDIDATE_BUFFER_TIMEOUT_MS = 5

export class Candidate {
  constructor(candidate) {
    this.candidate = candidate.candidate
    this.sdpMLineIndex = candidate.sdpMLineIndex
    this.sdpMid = candidate.sdpMid
  }

  toString() {
    return `candidate{mId=${this.sdpMid},mli=${this.sdpMLineIndex},attr=${this.candidate}}`
  }

  toJSON() {
    return {
      candidate: this.candidate,
      sdpMLineIndex: this.sdpMLineIndex,
      sdpMid: this.sdpMid,
    }
  }
}

export default class Candidater {
  constructor({send}) {
    this._send = send

    this._peerConnection = null
    this._earlyLocal = []
    this._earlyRemote = []
    this._localBuffer = []
    this._localBufferTimeout = 0

    this._onLocalCandidate = this._onLocalCandidate.bind(this)
    this._onBufferTimeout = this._onBufferTimeout.bind(this)
  }

  setPeerConnection(peerConnection) {
    this.reset()
    this._peerConnection = peerConnection
    this._peerConnection.addEventListener('icecandidate', this._onLocalCandidate)
  }

  handleRemoteCandidates(candidates) {
    if (!candidates) {
      return
    }
    candidates = candidates.map(candidate => new Candidate(candidate))

    if (!this._peerConnection) {
      log.warning(TAG, `got remote candidates without peer connection: ${candidates.join(', ')}`)
      return
    }

    if (this._peerConnection.hasRemoteDescription) {
      candidates.forEach(candidate => {
        log.debug(TAG, `adding remote candidate: ${candidate}`)
        this._peerConnection.addIceCandidate(candidate).catch(error => {
          log.error(TAG, `failed to add remote candidate: ${error}`)
        })
      })
    } else {
      log.debug(TAG, `buffering early remote candidates: ${candidates.join(', ')}`)
      this._earlyRemote = this._earlyRemote.concat(candidates)
    }
  }

  addEarlyRemoteCandidates() {
    log.debug(TAG, `adding early remote candidates: ${this._earlyRemote.join(', ')}`)

    this._earlyRemote.forEach(candidate => {
      this._peerConnection.addIceCandidate(candidate).catch(error => {
        log.error(TAG, `failed to add early remote candidate: ${error}`)
      })
    })

    this._earlyRemote = []
  }

  extractEarlyLocal() {
    if (!this._earlyLocal) {
      return []
    }
    let earlyLocal = this._earlyLocal
    this._earlyLocal = null
    log.debug(TAG, `extracted early local candidates: ${earlyLocal.join(', ')}`)
    return earlyLocal
  }

  reset() {
    if (this._localBufferTimeout) {
      clearTimeout(this._localBufferTimeout)
      this._localBufferTimeout = 0
    }
    if (this._peerConnection) {
      this._peerConnection.removeEventListener('icecandidate', this._onLocalCandidate)
      this._peerConnection = null
    }
    this._earlyRemote = []
    this._earlyLocal = []
  }

  _onLocalCandidate(event) {
    let {candidate} = event
    if (!candidate) {
      return
    }
    candidate = new Candidate(candidate)
    if (this._earlyLocal) {
      log.debug(TAG, `buffering candidates emitted before first description: ${candidate}`)
      this._earlyLocal.push(candidate)
    } else {
      this._localBuffer.push(candidate)
      if (!this._localBufferTimeout) {
        this._localBufferTimeout = setTimeout(this._onBufferTimeout, CANDIDATE_BUFFER_TIMEOUT_MS)
      }
    }
  }

  _onBufferTimeout() {
    this._localBufferTimeout = 0
    let candidates = this._localBuffer
    this._localBuffer = []
    log.debug(TAG, `sending candidates via server: ${candidates.join(', ')}`)
    this._send(candidates)
  }
}



