'use strict'
import log from 'common/log'
import EmitterMixin from 'common/emitter'
import CanvasFilter from 'media/canvasFilter'

const TAG = 'scaled-canvas-filter'

/**
 * A sink that can be used to make scaled down streams of existing
 * camera streams
 *
 * @class ScaledCanvasFilter
 */
export default class ScaledCanvasFilter extends EmitterMixin(CanvasFilter) {
  constructor(options) {
    super(options)
    let {
      projectionConfiguration,
      imageFrameRate,
    } = options

    this._projectionConfiguration = projectionConfiguration
    this._context = this._canvas.getContext('2d')
    this._imageFrameRate = imageFrameRate
    this._grabStillPicture = this._grabStillPicture.bind(this)
  }

  _calcProjection() {
    let {videoWidth, videoHeight} = this._video

    let {
      width,
      aspectRatio = 16 / 9,
      contentMode = 'aspectFill',  /* scaltToFill, aspectFit or aspectFill */
    } = this._projectionConfiguration

    let height = width / aspectRatio
    this._canvas.width = width
    this._canvas.height = height

    let videoAspectRatio = videoWidth / videoHeight

    if (contentMode === 'aspectFill') {
      if (videoAspectRatio < aspectRatio) {
        let delta = width / videoAspectRatio - height

        return {
          width: width,
          height: height + delta,
          deltaY: -delta / 2,
          deltaX: 0,
        }
      } else {
        let delta = height * videoAspectRatio - width

        return {
          height: height,
          width: width + delta,
          deltaY: 0,
          deltaX: -delta / 2,
        }
      }
    }

    if (contentMode === 'scaleToFill') {
      return {
        height: height,
        width: width,
        deltaY: 0,
        deltaX: 0,
      }
    }
  }

  startRender() {
    let {videoWidth, videoHeight} = this._video
    log.info(TAG, `video resolution: ${videoWidth}x${videoHeight}`)
    this._projection = this._calcProjection()

    if (this._imageFrameRate > 0) {
      log.info(TAG, `starting interval for still pictures: frameRate = ${1000 / this._imageFrameRate}`)
      this._stillPictureInterval = setInterval(this._grabStillPicture, 1000 / this._imageFrameRate)
    } else {
      log.info(TAG, 'not starting picture grab loop')
    }

    this._fpsInterval = 1000 / this._videoFrameRate
    this._then = Date.now()
    this._startTime = this._then
    this._render()
  }

  _grabStillPicture() {
    let dataUrl = this._canvas.toDataURL('image/joeg')
    this._emit('stillPictureCaptured', dataUrl)
  }

  render(video, canvas) {
    let {videoWidth, videoHeight} = this._video

    let ctx = this._context
    ctx.drawImage(video, 0, 0, videoWidth, videoHeight,
                  this._projection.deltaX, this._projection.deltaY,
                  this._projection.width, this._projection.height)
  }
}



