
import log from 'common/log'

const TAG = 'json-channel-ctrl'

export class JsonChannelBroadcastController {
  constructor({onMessageFromPeer, onPeerChannelOpen, onPeerChannelClose}) {
    this._peers = new Map()

    this._onPeerChannelOpen = onPeerChannelOpen
    this._onPeerChannelClose = onPeerChannelClose
    this._onMessageFromPeer = onMessageFromPeer
  }

  sendMessage(message) {
    this._peers.forEach(({channel}, peerId) => {
      if (channel.readyState === 'open') {
        channel.send(message).catch(error => {
          log.debug(TAG, `failed to send broadcast message to peer '${peerId}', ${error}`)
        })
      }
    })
  }

  sendMessageToPeer(peerId, message) {
    let peerInfo = this._peers.get(peerId)
    if (peerInfo) {
      peerInfo.channel.send(message).catch(error => {
        log.debug(TAG, `failed to send message to peer '${peerId}', ${error}`)
      })
      return true
    } else {
      return false
    }
  }

  addPeer(peerId, channel) {
    let peerInfo = {
      peerId,
      channel,
      messageListener: message => {
        this._onMessage(peerInfo, message)
      },
      readyStateListener: readyState => {
        this._onConnectionStateChange(peerInfo, readyState)
      },
    }
    this._peers.set(peerId, peerInfo)
    channel.on('message', peerInfo.messageListener)
    channel.on('readyState', peerInfo.readyStateListener)
  }

  removePeer(peerId) {
    if (!this._peers) return
    let peerInfo = this._peers.get(peerId)
    if (peerInfo) {
      this._peers.delete(peerId)

      peerInfo.channel.off('message', peerInfo.messageListener)
      peerInfo.channel.off('readyState', peerInfo.readyStateListener)
      peerInfo.messageListener = null
      peerInfo.readyStateListener = null
    }
  }

  _onConnectionStateChange(peerInfo, readyState) {
    if (readyState === 'open') {
      this._onPeerChannelOpen(peerInfo.peerId)
    } else if (readyState === 'close') {
      this._onPeerChannelClose(peerInfo.peerId)
    }
  }

  _onMessage(peerInfo, message) {
    this._onMessageFromPeer(peerInfo.peerId, message)
  }

  cleanup() {
    this._onPeerChannelOpen = null
    this._onPeerChannelClose = null
    this._onMessageFromPeer = null
    this._peers.forEach((_, peerId) => {
      this.removePeer(peerId)
    })
    this._peers.clear()
    this._peers = null
  }
}



