'use strict'

import log from 'common/log'
import argCheck from 'common/argCheck'
import {NotFoundError, NotAllowedError} from 'common/errors'

import {getUserMedia} from 'media/common'
import ConcreteMediaSource from 'media/concreteMediaSource'

const TAG = 'chrome-screen-source'

export function getChromeMediaSourceId(extensionId, sourceTypes) {
  return new Promise(function (resolve, reject) {
    const installedStatusTimeout = setTimeout(() => {
      window.removeEventListener('message', onMessage)
      let error = new NotFoundError('Screen sharing extension not installed')
      error.reason = new Error('Extension did not respond to install status check')
      reject(error)
    }, 1000)
    const onMessage = event => {
      var data = event.data
      if (data.extensionId === extensionId) {
        switch (data.message) {
          case 'extensionIsInstalled':
            clearTimeout(installedStatusTimeout)
            window.postMessage({
              extensionId, sourceTypes,
              message: 'requestChromeMediaSourceId',
            }, '*')
            break
          case 'chromeMediaSourceId':
            window.removeEventListener('message', onMessage)
            var sourceId = data.content
            if (sourceId) {
              resolve(sourceId)
            } else { // user denied the request
              reject(new NotAllowedError('The user denied the request to share the screen'))
            }
        }
      }
    }

    window.addEventListener('message', onMessage)
    window.postMessage({
      extensionId: extensionId,
      message: 'getInstalledStatus',
    }, '*')
  })
}

export function getChromeScreenConstraints({audio = false, video, sourceId} = {}) {
  if (!video) {
    video = {}
  }
  if (!(video.width || video.height)) {
    video.width = {max: window.screen.width}
    video.height = {max: window.screen.height}
  }
  video.chromeMediaSourceId = {exact: sourceId}
  video.chromeMediaSource = {exact: 'desktop'}

  return {audio, video}
}

function _sendChromeMessage(extensionId, message) {
  return new Promise((resolve, reject) => {
    window.chrome.runtime.sendMessage(extensionId, message, response => {
      if (response) {
        resolve(response)
      } else {
        let error = new Error('No response received')
        error.name = 'NotFoundError'
        reject(error)
      }
    })
  })
}

export function getChromeMediaSourceIdEx(extensionId, sourceTypes) {
  return new Promise(function (resolve, reject) {
    const installedStatusTimeout = setTimeout(() => {
      let error = new NotFoundError('Screen sharing extension not installed')
      error.reason = new Error('Extension did not respond to install status check')
      reject(error)
    }, 1000)

    _sendChromeMessage(extensionId,
      {
        extensionId,
        message: 'getInstalledStatus',
      }).then(response => {
        clearTimeout(installedStatusTimeout)
        return response
      }).then(_sendChromeMessage(extensionId,
        {
          extensionId,
          sourceTypes,
          message: 'requestChromeMediaSourceId',
        }).then(response => {
          var sourceId = response.content
          if (sourceId) {
            resolve(sourceId)
          } else { // user denied the request
            reject(new NotAllowedError('The user denied the request to share the screen'))
          }
        }).catch(error => reject(error))
      ).catch(error => reject(error))
  })
}

/**
 * Internal media node for acquiring a Chrome screen sharing stream.
 *
 * @private
 * @class ChromeScreenSource
 * @extends ConcreteMediaSource
 * @param {Object} options - Options object.
 * @param {string} options.chromeExtensionId - The id of the chrome extension to use.
 * @param {ScreenSharingType|null} [options.type=null] - The screen sharing type to use. Specifying
 *   null will let the user choose either screen or window.
 * @param {boolean} [options.audio=false] - Whether desktop audio should be captured as well.
 * @param {Object} [options.video] - Constraints for the video, defaults to max resolution and no
 *   framerate limit.
 */
export default class ChromeScreenSource extends ConcreteMediaSource {
  constructor(options) {
    super()

    argCheck.options('ChromeScreenSource constructor', 'options', options)
      .string('chromeExtensionId')
      .optString('type')
      .optBoolean('audio')
      .optObject('video')

    let sourceTypes = options.type ? [options.type] : ['screen', 'window']

    getChromeMediaSourceIdEx(options.chromeExtensionId, sourceTypes)
      .then(sourceId => getChromeScreenConstraints({
        sourceId,
        type: options.type,
        audio: options.audio,
        video: options.video,
      }))
      .then(constraints => getUserMedia(constraints))
      .then(
      stream => this.setStream(stream),
      error => {
        if (error.name && error.name === 'NotFoundError') {
          log.warning('deprecated', 'This Extension implementation will be DEPRICATED in later release')
          let p2 = getChromeMediaSourceId(options.chromeExtensionId, sourceTypes)
          this._handleExtensionId(p2, options).catch(() => {
            this.setError(error)
          })
        } else {
          this.setError(error)
        }
      }
      ).catch(error => {
        log.error(TAG, `unexpected error in ChromeScreenSource: ${error}`)
      })
  }
  _handleExtensionId(promise, options) {
    return promise.then(sourceId => getChromeScreenConstraints({
      sourceId,
      type: options.type,
      audio: options.audio,
      video: options.video,
    }))
      .then(constraints => getUserMedia(constraints))
      .then(
      stream => this.setStream(stream),
      error => this.setError(error)
      ).catch(error => {
        log.error(TAG, `unexpected error in ChromeScreenSource: ${error}`)
      })
  }
}



