'use strict'

import argCheck from 'common/argCheck'
import log from 'common/log'

import {ConfigurationData} from 'conference/raftConfiguration'

const TAG = 'raft'

export default class RaftLogEntry {
  static parse(json, index) {
    // configuration entries have 2 elements, op entries have 3
    if (json.length === 2) {
      let [term, data] = json
      return new ConfigurationEntry({term, index, data: ConfigurationData.parse(data)})
    } else if (json.length === 4) {
      let [term, id, op, args] = json
      return new OpEntry({term, index, id, op, args})
    } else {
      throw new TypeError(`Failed to parse log entry data, invalid length: ${json.length}`)
    }
  }

  constructor({term, index}) {
    if (this.constructor === RaftLogEntry) {
      throw new TypeError('RaftLogEntry should not by constructed directly')
    }
    this._term = term
    this._index = index
  }

  get term() {
    return this._term
  }

  get index() {
    return this._index
  }
}

export class ConfigurationEntry extends RaftLogEntry {
  constructor({term, index, data}) {
    super({term, index})
    argCheck.instance(this, 'data', data, ConfigurationData)
    this._data = data
  }

  get data() {
    return this._data
  }

  toString() {
    return `config{t=${this.term},i=${this.index},${this.data}}`
  }

  toJSON() {
    return [this.term, this.data]
  }
}

export const OP = {
  noop: 0,
  set: 1,
  append: 2,
}

const opNames = {
  0: 'noop',
  1: 'set',
  2: 'append',
}

export class OpEntry extends RaftLogEntry {
  static get idSize() {
    return 8
  }

  constructor({term, index, id = 0, op, args = []}) {
    super({term, index})
    this._id = id
    this._op = op
    this._args = args
  }

  get id() {
    return this._id
  }

  get op() {
    return this._op
  }

  get args() {
    return this._args
  }

  toString() {
    let name = opNames[this.op]
    let args = this.args.join(',')
    let shortId = this.id ? this.id.slice(0, 4) : '<no>'
    return `op{id=${shortId},t=${this.term},i=${this.index},${name},[${args}]}`
  }

  toJSON() {
    return [this.term, this.id, this.op, this.args]
  }

  apply(state) {
    if (this[this.op]) {
      log.debug(TAG, `applying ${this}`)
      return this[this.op](state, ...this.args)
    } else {
      throw new TypeError(`Failed to apply OpEntry, unknown op code: '${this.op}'`)
    }
  }

  // methods for each op to modify state, args are passed in as parameters 2+
  // If the state is modified it should always be returned, and the other way around.

  [OP.noop](state) {
  }

  [OP.set](state, key, value) {
    if (typeof(key) !== 'string') {
      throw new TypeError('Invalid OpEntry, set key must be a string')
    }
    if (typeof(value) === 'undefined') {
      delete state[key]
    }
    state[key] = value
    return state
  }

  [OP.append](state, key, value) {
    if (typeof(key) !== 'string') {
      throw new TypeError('Invalid OpEntry, append key must be a string')
    }
    if (typeof(value) === 'undefined') {
      return
    }
    if (state[key]) {
      state[key].push(value)
    } else {
      state[key] = [value]
    }
    return state
  }
}



