'use strict'

import log from 'common/log'
import {isChrome, errorForwarder} from 'common/utils'

const TAG = 'renegotiator'

export default class Renegotiator {
  constructor({label, channel, onError}) {
    this._label = label
    this._peerConnection = null
    this._isInitiator = null
    this._needRenegotiation = false
    this._doingRenegotiation = false
    this._renegotiationTime = 1
    this._renegotiationOfferQueue = []

    this._onError = onError

    this._channel = channel
    this._channel.on('message', this._onMessage.bind(this))

    this._onSignalingStateChange = this._onSignalingStateChange.bind(this)
    this._onNegotiationNeeded = this._onNegotiationNeeded.bind(this)
  }

  toString() {
    return `renegotiator{"${this._label}",initiator=${this._isInitiator},${this._peerConnection}}`
  }

  setPeerConnection(peerConnection) {
    this.reset()
    this._peerConnection = peerConnection
    this._peerConnection.addEventListener('negotiationneeded', this._onNegotiationNeeded)
    this._peerConnection.addEventListener('signalingstatechange', this._onSignalingStateChange)
  }

  start(isInitiator) {
    this._isInitiator = isInitiator

    if (this._needRenegotiation) {
      this._onNegotiationNeeded()
    }
  }

  reset() {
    if (this._peerConnection) {
      this._peerConnection.removeEventListener('negotiationneeded', this._onNegotiationNeeded)
      this._peerConnection.removeEventListener('signalingstatechange', this._onSignalingStateChange)
    }
    this._peerConnection = null
    this._isInitiator = null
    this._needRenegotiation = false
    this._doingRenegotiation = false
    this._renegotiationTime = 1
    this._renegotiationOfferQueue = []
  }

  _onNegotiationNeeded() {
    log.debug(TAG, this._peerConnection + ' needs negotiation')
    if (this._isInitiator === true) {
      if (this._doingRenegotiation) {
        this._needRenegotiation = true
      } else {
        this._sendRenegotiationOffer()
      }
    } else if (this._isInitiator === false) {
      this._sendRenegotiationRequest()
    } else {
      this._needRenegotiation = true
    }
  }

  _sendRenegotiationRequest() {
    this._renegotiationTime += 1
    let audioCount = 0
    let videoCount = 0
    this._peerConnection.getLocalStreams().forEach(stream => {
      audioCount += stream.getAudioTracks().length
      videoCount += stream.getVideoTracks().length
    })
    let time = this._renegotiationTime
    this._sendMessage('negotiationRequest', {audioCount, videoCount, time})
    log.debug(TAG, `sent negotiation request {time=${time},audio=${audioCount},video=${videoCount}}`)
  }

  _handleRenegotiationRequest(content) {
    var time = content.time
    if (time < this._renegotiationTime) {
      log.error(TAG, 'invalid renegotiation time: ' + time)
      return
    }
    if (isChrome) {
      this._requestedConstraints = {
        mandatory: {
          OfferToReceiveAudio: !!content.audioCount,
          OfferToReceiveVideo: !!content.videoCount,
        },
      }
    } else {
      this._requestedConstraints = {
        offerToReceiveVideo: content.videoCount || 0,
        offerToReceiveAudio: content.audioCount || 0,
      }
    }
    this._renegotiationTime = content.time
    if (!this._doingRenegotiation) {
      this._sendRenegotiationOffer()
    } else {
      this._needRenegotiation = true
    }
  }

  _sendRenegotiationOffer(offerConstraints) {
    this._doingRenegotiation = true
    this._needRenegotiation = false
    log.verbose(TAG, 'preparing to send renegotiation offer with constraints: ' +
      JSON.stringify(this._requestedConstraints))
    return this._peerConnection.performOffer({
      constraints: this._requestedConstraints,
    }).then(description => {
      log.debug(TAG, 'sending offer:', description)

      return this._sendMessage('offer', {
        type: description.type,
        sdp: description.sdp,
        unifiedPlanSdp: description.unifiedPlanSdp,
      })
    })
    .catch(errorForwarder(this._onError, '_sendRenegotiationOffer'))
  }

  _handleRenegotiationOffer(offer) {
    if (this._doingRenegotiation) {
      this._renegotiationOfferQueue.push(offer)
      return log.info(TAG, 'got early offer, queued:', offer)
    }

    log.verbose(TAG, 'got offer', offer)
    this._doingRenegotiation = true
    return this._peerConnection.performAnswer({offer})
      .then(description => {
        log.debug(TAG, 'sending answer:', description)
        return this._sendMessage('answer', {
          type: description.type,
          sdp: description.sdp,
          unifiedPlanSdp: description.unifiedPlanSdp,
          time: this._renegotiationTime,
        })
      })
      .catch(errorForwarder(this._onError, '_handleRenegotiationOffer'))
      .then(() => {
        this._doingRenegotiation = false
        if (this._renegotiationOfferQueue.length) {
          this._handleRenegotiationOffer(this._renegotiationOfferQueue.pop())
        }
      })
  }

  _handleRenegotiationAnswer(answer) {
    log.debug(TAG, 'got answer:', answer)
    return this._peerConnection.handleAnswer({answer})
      .catch(errorForwarder(this._onError, '_handleRenegotiationAnswer'))
      .then(() => {
        if (this._renegotiationTime > answer.time) {
          this._needRenegotiation = true
        } else {
          this._renegotiationTime = answer.time
        }
        this._doingRenegotiation = this._needRenegotiation
        if (this._needRenegotiation) {
          return this._sendRenegotiationOffer()
        }
      })
  }

  _onSignalingStateChange() {
    let {signalingState} = this._peerConnection
    log.debug(TAG, `${this} changed signaling state: ${signalingState}`)
  }

  _onMessage(message) {
    if (!this._peerConnection) {
      log.error(TAG, 'got signaling message without peer connection')
      return false
    }
    try {
      let {type, content} = message
      switch (type) {
        case 'negotiationRequest':
          this._handleRenegotiationRequest(content)
          break
        case 'answer':
          this._handleRenegotiationAnswer(content)
          break
        case 'offer':
          this._handleRenegotiationOffer(content)
          break
        default:
          log.warning(TAG, `got unknown message: '${type}'`)
          break
      }
    } catch (error) {
      log.error(TAG, 'error processing message (', event.data, '):', error, error.stack)
    }
  }

  _sendMessage(type, content) {
    if (this._channel.readyState === 'open') {
      this._channel.send({type, content}).catch(error => {
        log.debug(TAG, this, `failed to send '${type}' message, ${error}`)
      })
    }
  }
}



