'use strict'

import MediaSource from 'media/mediaSource'
import {audioContextUtil} from 'webrtc/utils'

/**
 * A dummy source that can generate a silent audio stream and a 1x1 black video stream.
 *
 * Useful for testing and working around browser bugs where streams have to be present.
 *
 * @class DummySource
 * @extends MediaSource
 * @param {Object} [options] - Optional options object
 * @param {boolean} [options.video=true] - Whether to generate dummy video.
 * @param {boolean} [options.audio=true] - Whether to generate dummy audio.
 */
export default class DummySource extends MediaSource {
  constructor({audio, video} = {audio: true, video: true}) {
    super()
    let tracks = []
    if (audio) {
      this._audioContext = audioContextUtil.get()
      this._destination = this._audioContext.createMediaStreamDestination()
      let [track] = this._destination.stream.getTracks()
      // track.enabled = false
      tracks.push(track)

      this._silence = this._audioContext.createBufferSource()
      this._silence.connect(this._destination, 0, 0)
    }
    if (video) {
      this._canvas = document.createElement('canvas')
      this._canvas.style.width = '1px'
      this._canvas.style.height = '1px'
      this._canvas.width = 1
      this._canvas.height = 1

      // Otherwise Firefox will complain about the canvas not being initialized
      this._canvas.getContext('2d')

      let stream = this._canvas.captureStream(1)
      let [track] = stream.getTracks()
      // track.enabled = false
      tracks.push(track)
    }
    this._stream = new MediaStream(tracks)
    this.setStream(this._stream)
  }

  get output() {
    return this._output
  }

  /**
   * Stops the source and cleans up all resources. This has to be called once the source
   * is no longer used in order to clean up resources.
   *
   * Calling this method multiple times has no effect.
   */
  stop() {
    if (this._stream) {
      this.setStream(null)
      this._stream.getTracks().forEach(track => track.stop())
      this._stream = null
      this._canvas = null
      if (this._audioContext) {
        audioContextUtil.release()
        this._audioContext = null
        this._destination = null
        this._silence.stop()
        this._silence = null
      }
    }
  }

  /**
   * @private
   * @function DummySource#setStream
   */
}



