'use strict'

// This module contains the channel registry, which
// is used to keep track of local and remote channels
// of different types, in different namespaces.

import log from 'common/log'

const TAG = 'datamux'

/**
 * Keeps track of pairing channels within a namespace and pairs them up.
 * Non-pairing channels also have a namespaceId, but we don't bother
 * tracking them here.
 */
export class PairingNamespace {
  constructor() {
    this._byPairingId = new Map()
    this._remoteInfos = new Map()
  }

  cleanup() {
    this._byPairingId.clear()
    this._byPairingId = null
    this._remoteInfos.clear()
    this._remoteInfos = null
  }

  clearRemoteInfos() {
    this._remoteInfos.clear()
    this._byPairingId.clear()
  }

  clearLocalChannels() {
    this._byPairingId.clear()
  }

  trimByRemoteId(minimumRemoteId) {
    this._remoteInfos.forEach((remoteInfo, key) => {
      if (remoteInfo.remoteId < minimumRemoteId) {
        this._remoteInfos.delete(key)
      }
    })
  }

  add(channel) {
    let pairingId = channel._pairingId
    if (!pairingId) {
      throw new Error('Tried to add channel without pairingId to pairing namespace')
    }
    let remoteInfo = this._remoteInfos.get(pairingId)
    if (remoteInfo) {
      channel._setRemoteId(remoteInfo.remoteId)
      this._remoteInfos.delete(pairingId)
    }
    this._byPairingId.set(pairingId, channel)
  }

  addPairingInfo({remoteId, label, protocol, pairingId}) {
    let channel = this._byPairingId.get(pairingId)
    if (channel) {
      if (channel.label !== label) {
        log.error(TAG, `label mismatch for pairing channel '${pairingId}', ${channel.label} != '${label}'`)
        return
      }
      if (channel.protocol !== protocol) {
        let values = `'${pairingId}', ${channel.protocol} != '${protocol}'`
        log.error(TAG, `protocol mismatch for pairing channel ${values}`)
        return
      }
      return channel
    } else {
      this._remoteInfos.set(pairingId, {remoteId, label, protocol})
    }
  }

  validateConfig({label, protocol, pairingId}) {
    if (this._byPairingId.has(pairingId)) {
      throw new Error(`Pairing channel id ${pairingId} already exists`)
    }
    let remoteInfo = this._remoteInfos.get(pairingId)
    if (remoteInfo) {
      if (remoteInfo.label !== label) {
        throw new Error('Pairing channel label does not match remote label, ' +
          `${remoteInfo.label} != ${label}`)
      }
      if (remoteInfo.protocol !== protocol) {
        throw new Error('Pairing channel protocol does not match remote protocol, ' +
          `${remoteInfo.protocol} != ${protocol}`)
      }
    }
  }
}

/**
 * Stores all channels and makes it possible to look them up by different IDs.
 *
 * Also responsible for linking local and remote channels.
 */
export default class MuxChannelRegistry {
  constructor() {
    this._channelIdCounter = 1

    // Any non-builtin channel which has or receives a remote id below
    // this number will be instantly closed
    this._minimumRemoteId = 0
    // Any channel below this ID may not be linked to a remote id
    this._minimumLocalId = 0

    // Builtin channels are created inside the library in a deterministic order
    // that should not change between protocol versions. The are used for internal
    // communication, and the purpose is to reduce setup time and bandwidth usage.
    this._builtinChannels = new Map()

    // Keeps track of local channels
    this._localChannels = new Map()

    // Tracks all channels, but resets on disconnect
    this._byRemoteId = new Map()
    this._namespaces = new Map()
  }

  toString() {
    return `channelRegistry{local=${this._localChannels.size},remote=${this._byRemoteId.size}}`
  }

  cleanup() {
    this.clearConnections()
    this._builtinChannels.clear()
    this._builtinChannels = null
    this._localChannels.clear()
    this._localChannels = null
    this._byRemoteId = null
    this._namespaces.forEach(namespace => namespace.cleanup())
    this._namespaces.clear()
    this._namespaces = null
  }

  clearConnections() {
    this._builtinChannels.forEach(channel => channel._close())
    this._localChannels.forEach(channel => channel._close())
    this._localChannels.clear()
    this._namespaces.forEach(namespace => namespace.clearRemoteInfos())
    this._namespaces.clear()
    this._byRemoteId.clear()
  }

  // Clears all paired and normal channels, but keeps builtin ones
  // Returns a channel id to use as the minimum id by the peer.
  clearAppConnections() {
    this._localChannels.forEach(channel => channel._close())
    this._localChannels.clear()
    this._namespaces.forEach(namespace => namespace.clearLocalChannels())

    this._byRemoteId.forEach(channel => {
      if (!channel._builtin) {
        channel._close()
      }
    })
    // We clear everything and then add back the buildin ones
    this._byRemoteId.clear()
    this._builtinChannels.forEach(channel => {
      if (channel._remoteId) {
        this._byRemoteId.set(channel._remoteId, channel)
      }
    })

    this._minimumLocalId = this._channelIdCounter

    return this._channelIdCounter
  }

  clearNamespace(namespaceId) {
    let namespace = this._namespaces.get(namespaceId)
    if (namespace) {
      namespace.cleanup()
      this._namespaces.delete(namespaceId)
    }
    const removeChannel = channel => {
      if (channel._namespaceId === namespaceId) {
        this.remove(channel)
        if (channel._remoteId) {
          this._byRemoteId.delete(channel._remoteId)
        }
        this._localChannels.delete(channel._localId)
        channel._close()
      }
    }
    this._localChannels.forEach(removeChannel)
    this._byRemoteId.forEach(removeChannel)
  }

  setMinimumRemoteId(minimumRemoteId) {
    this._minimumRemoteId = minimumRemoteId

    this._byRemoteId.forEach((channel, remoteId) => {
      if (channel._builtin) {
        return
      }
      if (channel._remoteId && channel._remoteId < minimumRemoteId) {
        channel._close()
        this._byRemoteId.delete(remoteId)
      }
    })

    this._namespaces.forEach(namespace => namespace.trimByRemoteId(minimumRemoteId))
  }

  getNextId() {
    let channelId = this._channelIdCounter
    this._channelIdCounter += 1
    return channelId
  }

  getByRemoteId(id) {
    return this._byRemoteId.get(id)
  }

  forEachLocalChannel(func, thisArg) {
    this._localChannels.forEach(func, thisArg)
  }

  connectBuiltins() {
    this._builtinChannels.forEach(channel => {
      this._byRemoteId.set(channel._localId, channel)
      channel._setRemoteId(channel._localId)
    })
  }

  addBuiltin(channel) {
    this._builtinChannels.set(channel._localId, channel)
  }

  addLocal(channel) {
    this._localChannels.set(channel._localId, channel)
    if (channel._pairingId) {
      this._getPairingNamespace(channel._namespaceId).add(channel)
    }
    if (channel._remoteId) { // might be set in namespace if pairing
      this._byRemoteId.set(channel._remoteId, channel)
    }
  }

  addRemote({channel, remoteId}) {
    if (remoteId < this._minimumRemoteId) {
      log.info(TAG, `won't add channel with remote id ${remoteId}, minimum is ${this._minimumRemoteId}`)
      return
    }
    let oldRemoteId = channel._remoteId
    channel._setRemoteId(remoteId)
    if (oldRemoteId) {
      this._byRemoteId.delete(oldRemoteId)
    }
    this._byRemoteId.set(channel._remoteId, channel)
  }

  remove(channel) {
    if (channel._remoteId) {
      if (!this._byRemoteId.delete(channel._remoteId)) {
        log.error(TAG, `could not find channel to remove with remote id '${channel._remoteId}'`)
      }
    }
    if (channel._isLocal) {
      this._localChannels.delete(channel._localId)
    }
  }

  link({localId, remoteId}) {
    if (remoteId < this._minimumRemoteId) {
      log.info(TAG, `won't link channel with remote id ${remoteId}, minimum is ${this._minimumRemoteId}`)
      return
    }
    if (localId < this._minimumLocalId) {
      log.info(TAG, `won't link channel with local id ${localId}, minimum is ${this._minimumLocalId}`)
      return
    }
    let channel = this._localChannels.get(localId)
    if (channel) {
      channel._setRemoteId(remoteId)
      this._byRemoteId.set(remoteId, channel)
    } else {
      log.warning(TAG, `link channel with local id '${localId}' not found for remote id ${remoteId}`)
    }
  }

  validateConfig({label, protocol, namespaceId, pairingId}) {
    if (pairingId) {
      this._getPairingNamespace(namespaceId).validateConfig({label, protocol, pairingId})
    }
  }

  addPairingInfo({remoteId, namespaceId, label, protocol, pairingId}) {
    if (remoteId < this._minimumRemoteId) {
      log.info(TAG, `won't add pairing info with remote id ${remoteId}, minimum is ${this._minimumRemoteId}`)
      return
    }
    let channel = this._getPairingNamespace(namespaceId).addPairingInfo({remoteId, label, protocol, pairingId})
    if (channel) {
      let oldRemoteId = channel._remoteId
      channel._setRemoteId(remoteId)
      if (oldRemoteId) {
        this._byRemoteId.delete(oldRemoteId)
      }
      this._byRemoteId.set(channel._remoteId, channel)
    }
  }

  _getPairingNamespace(namespaceId) {
    let namespace = this._namespaces.get(namespaceId)
    if (!namespace) {
      namespace = new PairingNamespace(namespaceId)
      this._namespaces.set(namespaceId, namespace)
    }
    return namespace
  }
}



