'use strict'

import argCheck from 'common/argCheck'
import log from 'common/log'
import {priv} from 'common/utils'

import {AdapterJS} from 'webrtc/utils'
const {WebRTCPlugin} = AdapterJS

import {getUserMedia} from 'media/common'
import ConcreteMediaSource from 'media/concreteMediaSource'
import ScreenSourceInfo from 'media/screenSourceInfo'

const TAG = 'temasys-screen-source'

class TemasysScreenSourceInfo extends ScreenSourceInfo {
  constructor(priv, info) {
    super(priv, {
      type: info.kind,
      label: info.label,
    })
    this._id = info.id
  }

  toString() {
    return `temasysScreenSourceInfo{type=${this.type},id=${this._id},label="${this.label}"}`
  }
}

/**
 * Internal media node for acquiring a Temasys screen sharing stream.
 *
 * @private
 * @class TemasysScreenSource
 * @extends ConcreteMediaSource
 * @param {Object} [options] - Optional options object.
 * @param {ScreenSharingType|null} [options.type=null] - The screen sharing type to use. Specifying
 *   null will let the user choose either screen or window.
 * @param {boolean} [options.audio=false] - Whether audio should be requested as well.
 * @param {Object} [options.video] - Constraints for the video, defaults to max resolution and no
 *   framerate limit.
 */
export default class TemasysScreenSource extends ConcreteMediaSource {
  static get canEnumerateSources() {
    return true
  }

  static enumerateSources() {
    return new Promise(resolve => {
      WebRTCPlugin.plugin.getScreensharingSources(list => {
        resolve(list.map(info => priv(TemasysScreenSourceInfo, info)))
      })
    })
  }

  constructor(options) {
    super()

    argCheck.optOptions('TemasysScreenSource constructor', 'options', options)
      .optObject('video')

    const video = options.video || {}
    const type = options.type || 'screensharing'

    if (!video.mediaSource) {
      video.mediaSource = type
    }

    if (options.type && !WebRTCPlugin.plugin.screensharingKeys) {
      log.warning(TAG, 'this version of the temasys plugin does not support screen sharing type selection')
    }
    video.optional = [{
      sourceId: WebRTCPlugin.plugin.screensharingKey || 'Screensharing',
    }]

    if (video.source) {
      var source = video.source
      if (source.exact) {
        throw new TypeError('Constaint video.source.exact is not supported')
      } else if (source.ideal) {
        source = source.ideal
      }
      argCheck.instance(this, 'source', video.source, TemasysScreenSourceInfo)
      video.optional.push({
        screenId: video.source._id,
      })
      delete video.source
    }

    getUserMedia({
      video,
      audio: false,
    }).then(stream => {
      this.setStream(stream)
      stream.onended = () => {
        if (stream) stream.stop()
        this.setStream(null)
      }
    }, error => this.setError(error)).catch(error => {
      log.error(TAG, `unexpected error in TemasysScreenSource: ${error}`)
    })
  }

  // track.stop() for screen sharing streams is broken at least up until 0.8.892
  _stopStream(stream) {
    try {
      stream.stop()
    } catch (error) {
      log.error(TAG, 'failed to stop media stream:', error)
    }
  }
}



