'use strict'

import argCheck from 'common/argCheck'
import log from 'common/log'
import {diff} from 'common/utils'

import {Votes} from 'conference/raftConfiguration'
import {ConfigurationEntry, OP} from 'conference/raftLogEntry'
import {
  AppendEntriesRequest,
  PreVoteRequest,
  RequestVoteRequest,
  StateOpResponse,
} from 'conference/raftMessages'

const TAG = 'raft'

export const EXPECTED_RTT_MS = 200

// This doesn't need to be particularly low, but it's good to get rid
// of old followers so that we don't leak into unused send buffers,
// It should however be high enough that the follower has a chance to
// reconnect, so we set it higher than datachannel and ICE timeouts.
const FOLLOWER_REMOVE_TIMEOUT_MS = 15000

export default class RaftState {
  constructor(log, send, term) {
    this._log = log
    this._send = send
    this._term = argCheck.number(this, 'term', term)
  }

  toFollower(term, leader, votedFor) {
    if (this.stop) {
      this.stop()
    }
    return new FollowerState(this._log, this._send, term, leader, votedFor)
  }

  toCandidate(term, preVoteDone) {
    if (this.stop) {
      this.stop()
    }
    return new CandidateState(this._log, this._send, term, preVoteDone)
  }

  toLeader(term, configuration) {
    if (this.stop) {
      this.stop()
    }
    return new LeaderState(this._log, this._send, term, configuration)
  }

  handleAppendEntriesRequest(peerId, message) {
    let response = this._log.handleAppendEntriesRequest(message)
    if (response) {
      this._send(peerId, response)
      return this.toFollower(message.term, peerId)
    }
  }

  handleAppendEntriesResponse(peerId, message) {
  }

  handleRequestVoteRequest(peerId, message) {
    let granted = message.compareLog(this._log) >= 0
    if (message.term === this._term) {
      if (this._votedFor === peerId) {
        // If we already voted for the peer this term, meaning we are a follower, resend the vote
        log.debug(TAG, this, `resending granted vote to peer, '${peerId}'`)
        this._send(peerId, message.response({granted: true}))
      } else if (this._votedFor) {
        // Or if we already voted for someone else, reject the vote
        log.debug(TAG, this, `rejecting vote for '${peerId}', already voted for ${this._votedFor}`)
        this._send(peerId, message.response({granted: false}))
      } else if (this instanceof FollowerState) {
        // If we are follower at this point, it means we rejected a vote for this term,
        // but may still select someone else to vote for.
        log.debug(TAG, this, `${granted ? 'granting' : 'rejecting'} same-term vote request from '${peerId}'`)
        this._send(peerId, message.response({granted}))
        if (granted) {
          this._votedFor = peerId
          return this
        }
      } else {
        // Otherwise we are either leader or candidate and should reject the vote
        log.debug(TAG, this, `ignoring vote request from '${peerId}', already leader`)
        this._send(peerId, message.response({granted: false}))
      }
    } else if (message.term <= this._term) {
      // If the vote is for an old term, reject it
      this._send(peerId, message.response({granted: false}))
    } else {
      // We received a vote request for a new term, but need to figure out if logs are up to date
      // TODO: we may want to ignore votes when we're still hearing from the leader
      this._send(peerId, message.response({granted}))
      if (this instanceof FollowerState) {
        this._term = argCheck.number('RaftState.handleRequestVoteRequest', 'message.term', message.term)
        this._leader = null
        if (granted) {
          this._votedFor = peerId
          return this
        }
      } else {
        return this.toFollower(message.term, null, granted ? peerId : null)
      }
    }
  }

  handleRequestVoteResponse(peerId, message) {
  }

  handlePreVoteRequest(peerId, message) {
    let granted = false
    let reason = 'none'

    if (message.term < this._term) {
      // If the vote is for an old term, reject it
      granted = false
      reason = 'old or current term'
    } else {
      // We received a vote request for a new term, but need to figure out if logs are up to date
      granted = message.compareLog(this._log) >= 0
      reason = 'newer term'
    }
    log.debug(TAG, this, `${granted ? 'granting' : 'rejecting'} pre-vote request from '${peerId}' when ${reason}`)
    this._send(peerId, message.response({granted}))
  }

  handlePreVoteResponse(peerId, message) {
  }

  handleSnapshotRequest(peerId, message) {
  }

  handleSnapshotResponse(peerId, message) {
  }

  handleStateOpRequest(peerId, message) {
    this._send(peerId, message.response({errorCode: StateOpResponse.NOT_LEADER}))
  }

  // StateOpResponse is handled by StateMachine

  forwardStateOpRequest(request) {
  }

  hasVote(peerId) {
    return false
  }
}

export class FollowerMapEntry {
  constructor({term, nextIndex, topologyInfo, log, send, remove}) {
    this._term = term
    this._log = log
    this._send = send
    this._removeSelf = remove
    this._nextIndex = argCheck.number(this, 'nextIndex', nextIndex)
    this._matchIndex = 0
    this._topologyInfo = topologyInfo
    this._timeoutId = null
    this._removeTimeoutId = null
    this._idle = true
    this._removeIndex = Infinity
  }

  get matchIndex() {
    return this._matchIndex
  }

  start() {
    this._startTimeout()
    this._sendAppendRequest(0)
  }

  stop() {
    if (this._timeoutId) {
      clearTimeout(this._timeoutId)
      this._timeoutId = null
    }
    if (this._removeTimeoutId) {
      clearTimeout(this._removeTimeoutId)
      this._removeTimeoutId = null
    }
    this._topologyInfo._invalidate()
  }

  // Followers that are removed should receive the information that they
  // have been removed, so we keep sending messages until we have received
  // a matchIndex that confirms that they have been informed of their own
  // removal.
  // By always using the minimum index it's safe to call this multiple
  // times as as configurations change.
  phaseOutUntilIndex(index) {
    if (this._removeIndex !== Infinity) {
      this._removeIndex = Math.min(this._removeIndex, index)
      this._removeTimeoutId = setTimeout(() => {
        this._removeTimeoutId = null
        this._removeSelf()
      }, FOLLOWER_REMOVE_TIMEOUT_MS)
    }
  }

  handleAppendEntriesResponse(message) {
    this._topologyInfo._update(message)

    if (message.success) {
      this._matchIndex = message.matchIndex
      this._nextIndex = message.matchIndex + 1
      if (this._log.lastLogIndex > this._nextIndex) {
        this._sendAppendRequest(5)
      } else {
        this._idle = true
      }
    } else {
      this._nextIndex = message.matchIndex
      if (this._nextIndex < 1) {
        this._nextIndex = 1
      }
      this._sendAppendRequest(0)
    }

    if (this._matchIndex >= this._removeIndex) {
      clearTimeout(this._removeTimeoutId)
      this._removeTimeoutId = null
      this._removeSelf()
    }
  }

  _onAppend(toIndex) {
    // Will only trigger a send if we have received acks for all messages
    if (this._nextIndex === toIndex) {
      if (this._idle) {
        log.verbose(TAG, this, 'was idle when matching entries were added, sending')
        this._sendAppendRequest(5)
      } else {
        log.verbose(TAG, this, 'was busy when matching entries were added')
      }
    }
  }

  _sendAppendRequest(maxEntries = 0) {
    let prevIndex = this._nextIndex - 1
    let prevTerm = this._log.entryTermAtIndex(prevIndex)
    let entries = this._log.sliceEntries(this._nextIndex, this._nextIndex + maxEntries)

    let maxCommitIndex = this._log.commitIndex
    let lastEntryIndex = prevIndex + entries.length
    let commitIndex = Math.min(maxCommitIndex, lastEntryIndex)

    this._send(AppendEntriesRequest.create({
      term: this._term,
      topologyIndex: this._topologyInfo.topologyIndex,
      prevIndex,
      prevTerm,
      commitIndex,
      entries,
    }))

    this._idle = false
  }

  _startTimeout() {
    if (this._timeoutId) {
      clearTimeout(this._timeoutId)
    }
    this._timeoutId = setTimeout(() => {
      this._startTimeout()
      this._sendAppendRequest(1)
    }, EXPECTED_RTT_MS * 4)
  }
}

/*
  ##       ########    ###    ########  ######## ########
  ##       ##         ## ##   ##     ## ##       ##     ##
  ##       ##        ##   ##  ##     ## ##       ##     ##
  ##       ######   ##     ## ##     ## ######   ########
  ##       ##       ######### ##     ## ##       ##   ##
  ##       ##       ##     ## ##     ## ##       ##    ##
  ######## ######## ##     ## ########  ######## ##     ##
*/

export class LeaderState extends RaftState {
  constructor(log, send, term = 0) {
    super(log, send, term)
    this._onTopologyChange = this._onTopologyChange.bind(this)

    // TODO: Leader should also have an election timeout that is triggered if not
    // a majority of the followers respond.
    this._followerMap = new Map()
    this._joinerMap = new Map()
    this._pendingAdditions = new Set()
    this._pendingRemovals = new Set()

    this.start()
  }

  toString() {
    return `leader{term=${this._term}}`
  }

  start() {
    this._log.createOpEntry({term: this._term, op: OP.noop})
    this._afterAppend()
    let nextIndex = this._log.lastLogIndex + 1
    this._log.meshTopology.activate()
    this._log.meshTopology.addChangeListener(this._onTopologyChange)
    this._log.configuration.forEachPeer(peerId => this._addFollower(peerId, nextIndex))
  }

  stop() {
    this._followerMap.forEach(follower => follower.stop())
    this._followerMap.clear()
    this._joinerMap.forEach(intervalId => clearInterval(intervalId))
    this._joinerMap.clear()
    this._log.meshTopology.removeChangeListener(this._onTopologyChange)
    this._log.meshTopology.deactivate()
  }

  _onTopologyChange(topology, changedUpState) {
    if (!changedUpState) {
      // Leader only cares about connections going up and down, rtt/bw is for relaying
      return
    }

    log.verbose(TAG, this, 'got significant topology change, checking if any members should be removed')

    // First we figure out what member connections we are missing between ourselves and each follower
    let missingConnections = new Set()
    let localLinks = topology.localTopologyInfo.links
    // this._followerMap.forEach((follower, peerId) => {
    this._log.configuration.members.forEach(peerId => {
      if (peerId === this._log.configuration.ownId) {
        return
      }
      let localLink = localLinks[peerId]
      if (localLink && localLink.up) {
        return
      }
      if (this._pendingRemovals.has(peerId)) {
        return
      }
      missingConnections.add(peerId)
    })

    if (missingConnections.size === 0) {
      return
    }

    // Look at the link infos for the rest of the cluster and keep all connections
    // where some other conference member has a conneciton.
    topology.topologyInfos.forEach(({links}, peerId) => {
      // If our link to the peer is down we don't trust the current peer link info
      let localLink = localLinks[peerId]
      if (!localLink || !localLink.up) {
        return
      }
      if (this._followerMap.has(peerId)) {
        missingConnections.forEach(missingPeerId => {
          if (missingPeerId === peerId) {
            return
          }
          if (links[missingPeerId] && links[missingPeerId].up) {
            log.debug(TAG, this, `not removing connection to ${missingPeerId} because ${peerId} has a connection`)
            missingConnections.delete(missingPeerId)
          }
        })
      }
    })

    log.info(TAG, this, `removing connections to [${[...missingConnections].join(',')}] with topology: ${topology}`)
    // Now we're left with all peers that should be removed
    missingConnections.forEach(peerId => {
      this._pendingRemovals.add(peerId)
    })
    this._handlePendingMembershipUpdates()
  }

  // When adding a peer it will always be in the joiner state. This is because a joiner
  // does not transition to follower until a configuration with their id has been committed.
  // Meaning that if we manager to get elected as leader, we will either see the peer in the
  // configuration and ignore the add request, or we won't and the peer must still be in
  // the joiner state.
  addPeer({peerId, mustRejoin = true}) {
    if (this._followerMap.has(peerId)) {
      let isMember = this._log.configuration.members.has(peerId)
      let localLink = this._log.meshTopology.localTopologyInfo.links[peerId]
      if (isMember && localLink && localLink.up && !mustRejoin) {
        log.debug(TAG, this, 'got add request for follower, but the link is up, ignoring')
        return
      } else {
        log.debug(TAG, this, `got add request for follower, ${peerId}, resetting state`)
        this._removeFollower(peerId)
      }
    }
    if (this._joinerMap.has(peerId)) {
      log.debug(TAG, this, `got a second request to add peer ${peerId}`)
      return
    }
    log.debug(TAG, this, `added peer to joiners: ${peerId}`)

    let sendSnapshot = () => {
      let snapshot = this._log.createSnapshot(this._term)
      this._send(peerId, snapshot)
    }
    let intervalId = setInterval(sendSnapshot, EXPECTED_RTT_MS * 4)
    this._joinerMap.set(peerId, intervalId)
    sendSnapshot()
  }

  appendConfiguration(data) {
    let oldMembers = this._log.configuration.members
    let entry = this._log.createConfigurationEntry({term: this._term, data})
    let newMembers = this._log.configuration.members

    let [addedPeers, removedPeers] = diff(oldMembers, newMembers)
    let nextIndex = this._log.lastLogIndex + 1

    addedPeers.forEach(peerId => this._addFollower(peerId, nextIndex))
    removedPeers.forEach(peerId => {
      let follower = this._followerMap.get(peerId)
      if (follower) {
        follower.phaseOutUntilIndex(nextIndex)
      }
    })
    this._afterAppend()
    return entry
  }

  _afterAppend() {
    let index = this._log.lastLogIndex
    this._followerMap.forEach(follower => {
      follower._onAppend(index)
    })
    this._tryCommit()
  }

  handleAppendEntriesRequest(peerId, message) {
    if (message.term === this._term) {
      log.debug(TAG, `ERROR: ${this} received append request for own term: ${message}`)
    } else if (message.term > this._term) {
      // A new leader was elected, allow it to take over
      return super.handleAppendEntriesRequest(peerId, message)
    }
  }

  handleAppendEntriesResponse(peerId, message) {
    if (message.term === this._term) {
      // TODO: Add tests for this
      if (message.needSnapshot) {
        log.debug(TAG, this, `received append entries response from ${peerId} with needSnapshot flag`)
        this.addPeer({peerId, mustRejoin: true})
        return
      }
      // Got a response to one of our append requests
      let follower = this._followerMap.get(peerId)
      if (follower) {
        follower.handleAppendEntriesResponse(message)
        if (message.success) {
          // We have appended new log entires, might be able to increment commit index
          this._tryCommit()
        }
      } else {
        log.debug(TAG, `ERROR: ${this} got append entries response from unknown peer: ${peerId}`)
      }
    } else if (message.term > this._term) {
      log.debug(TAG, `ERROR: ${this} received append response for newer term: ${message}`)
    }
  }

  handleSnapshotResponse(peerId, message) {
    if (message.term !== this._term) {
      log.warning(TAG, this, `received snapshot response for future term: ${message}`)
      return
    }
    let intervalId = this._joinerMap.get(peerId)
    if (!intervalId) {
      log.debug(TAG, this, `received snapshot response for missing joiner: ${message}`)
      return
    }
    clearInterval(intervalId)
    this._joinerMap.delete(peerId)

    log.debug(TAG, this, `accepted snapshot response from ${peerId}: ${message}`)
    let {lastIndex} = message
    this._addFollower(peerId, lastIndex + 1)

    this._pendingAdditions.add(peerId)
    this._handlePendingMembershipUpdates()
  }

  handleStateOpRequest(peerId, message) {
    this.forwardStateOpRequest(message)
    this._send(peerId, message.response())
  }

  forwardStateOpRequest(request) {
    let {id, op, args} = request
    this._log.createOpEntry({term: this._term, id, op, args})
    this._afterAppend()
  }

  _tryCommit() {
    let matchIndices = {
      [this._log.configuration.ownId]: this._log.lastLogIndex,
    }
    this._followerMap.forEach((follower, clientId) => {
      matchIndices[clientId] = follower.matchIndex
    })
    let majorityMatchIndex = this._log.configuration.getMajorityIndex(matchIndices)
    let committed = this._log.tryCommitIndexForTerm(majorityMatchIndex, this._term)

    if (committed && committed.length) {
      this._followerMap.forEach(follower => follower._sendAppendRequest(0))
    }

    if (committed && committed.some(entry => entry instanceof ConfigurationEntry)) {
      // This assumes that there can only be a single un-committed configuration log entry
      // at any given time for any leader. Afaik that is true but needs some proof.
      this._handlePendingMembershipUpdates()
    }
  }

  _handlePendingMembershipUpdates() {
    if (this._pendingAdditions.size + this._pendingRemovals.size === 0) {
      return
    }

    let members = this._log.configuration.members

    // The configuration implementation in the first Raft paper is more
    // complex and less robus than the one in the thesis.
    // The basic concept of the one from the thesis is that as long as
    // we do few enough additions or removals each time, it is safe.
    //
    // TODO: It's possible to add or remove more than one members at
    // at time in large clusters, figure out limits.
    if (this._pendingRemovals.size) {
      let [removed] = [...this._pendingRemovals]
      this._pendingRemovals.delete(removed)
      members.delete(removed)
    } else if (this._pendingAdditions.size) {
      let [added] = [...this._pendingAdditions]
      this._pendingAdditions.delete(added)
      members.add(added)
    }

    let data = this._log.configuration.createMembersChange(members)
    this.appendConfiguration(data)
  }

  _addFollower(peerId, nextIndex) {
    if (this._followerMap.has(peerId)) {
      log.debug(TAG, this, `not adding new follower, already exists since joining: ${peerId}`)
      return
    }
    log.debug(TAG, this, `added peer to followers: ${peerId}`)
    const send = message => this._send(peerId, message)
    let topologyInfo = this._log.meshTopology.addClient(peerId)
    let follower = new FollowerMapEntry({
      term: this._term,
      nextIndex,
      topologyInfo,
      log: this._log,
      send,
      remove: this._removeFollower.bind(this, peerId),
    })
    this._followerMap.set(peerId, follower)
    follower.start()
  }

  _removeFollower(peerId) {
    let follower = this._followerMap.get(peerId)
    if (follower) {
      follower.stop()
      this._followerMap.delete(peerId)
      this._log.meshTopology.removeClient(peerId)
    } else {
      log.warning(TAG, this, `failed to remove missing follower: ${peerId}`)
    }
  }
}

/*
  ########  #######  ##       ##        #######  ##      ## ######## ########
  ##       ##     ## ##       ##       ##     ## ##  ##  ## ##       ##     ##
  ##       ##     ## ##       ##       ##     ## ##  ##  ## ##       ##     ##
  ######   ##     ## ##       ##       ##     ## ##  ##  ## ######   ########
  ##       ##     ## ##       ##       ##     ## ##  ##  ## ##       ##   ##
  ##       ##     ## ##       ##       ##     ## ##  ##  ## ##       ##    ##
  ##        #######  ######## ########  #######   ###  ###  ######## ##     ##
*/

export class FollowerState extends RaftState {
  constructor(log, send, term = 0, leader = null, votedFor = null) {
    super(log, send, term)
    argCheck.optString(this, 'leader', leader)

    this._sendPreVoteRequests = this._sendPreVoteRequests.bind(this)

    this._leader = leader
    this._votedFor = votedFor

    // When doing a pre-vote, this is the term we would candidate for
    this._targetTerm = null
  }

  toString() {
    return `follower{term=${this._term},leader=${this._leader},votedFor=${this._votedFor}}`
  }

  hasVote(peerId) {
    return this._votedFor === peerId
  }

  toFollower(term, leader, votedFor = null) {
    if (term > this._term) {
      this._term = term
      this._leader = leader
      this._votedFor = votedFor
    }
    if (term === this._term && !this._leader) {
      this._leader = leader
    }
    this.stop()
    return this
  }

  // The below code is a part of the pre-vote modification of Raft, see raftMessages.js
  toCandidate(term, preVoteDone) {
    if (preVoteDone) {
      return super.toCandidate(term, preVoteDone)
    }
    this.stop()
    this._targetTerm = term
    this._preVotes = new Votes(this._log.configuration.ownId)
    this.start()
    return this
  }

  start() {
    this._sendPreVoteRequests()
    this._intervalId = setInterval(this._sendPreVoteRequests, EXPECTED_RTT_MS * 4)
  }

  stop() {
    if (this._intervalId) {
      clearInterval(this._intervalId)
      this._intervalId = null
    }
    if (this._targetTerm) {
      this._targetTerm = null
      this._preVotes = null
    }
  }

  handlePreVoteResponse(peerId, message) {
    if (this._targetTerm && message.term === this._targetTerm) {
      // We got a vote for the current term, so we store the reply and check if we
      // have received enough votes to have a majority and become leader
      this._preVotes.set(peerId, message.granted)
      log.debug(TAG, this, `received pre-vote reply from ${peerId}: ${message.granted ? 'granted' : 'rejected'}`)
      if (this._log.configuration.hasMajority(this._preVotes.grantedVotes)) {
        return super.toCandidate(this._targetTerm, true)
      }
    }
  }

  forwardStateOpRequest(request) {
    if (this._leader) {
      this._send(this._leader, request)
    } else {
      // We just let the client time out for now
      // TODO: Use an async queue to serialize messages and send feedback
    }
  }

  _sendPreVoteRequests() {
    let request = PreVoteRequest.create({
      term: this._targetTerm, // we're going to be voting in the next term
      lastLogTerm: this._log.lastLogTerm,
      lastLogIndex: this._log.lastLogIndex,
    })

    this._log.configuration.forEachPeer(peerId => {
      if (!this._preVotes.has(peerId)) {
        this._send(peerId, request)
      }
    })
  }
}

/*
   ######     ###    ##    ## ########  #### ########     ###    ######## ########
  ##    ##   ## ##   ###   ## ##     ##  ##  ##     ##   ## ##      ##    ##
  ##        ##   ##  ####  ## ##     ##  ##  ##     ##  ##   ##     ##    ##
  ##       ##     ## ## ## ## ##     ##  ##  ##     ## ##     ##    ##    ######
  ##       ######### ##  #### ##     ##  ##  ##     ## #########    ##    ##
  ##    ## ##     ## ##   ### ##     ##  ##  ##     ## ##     ##    ##    ##
   ######  ##     ## ##    ## ########  #### ########  ##     ##    ##    ########
*/

export class CandidateState extends RaftState {
  constructor(log, send, term, preVoteDone) {
    super(log, send, term)

    this._tick = this._tick.bind(this)

    // Start by gathering a majority of pre-votes, otherwise we wait until next election.
    // Pre-votes are a modification to Raft, see raftMessages.js
    this._preVoteDone = preVoteDone
    this._preVotes = preVoteDone ? null : new Votes(log.configuration.ownId)
    this._votes = preVoteDone ? new Votes(log.configuration.ownId) : null
    this._intervalId = null

    this.start()
  }

  toString() {
    return `candidate{term=${this._term},votes=${this._votes}}`
  }

  start() {
    this._tick()
    this._intervalId = setInterval(this._tick, EXPECTED_RTT_MS * 4)
  }

  stop() {
    if (this._intervalId) {
      clearInterval(this._intervalId)
      this._intervalId = null
    }
  }

  _tick() {
    let requestFactory = this._preVoteDone ? RequestVoteRequest : PreVoteRequest
    let voteCollection = this._preVoteDone ? this._votes : this._preVotes

    let request = requestFactory.create({
      term: this._term,
      lastLogTerm: this._log.lastLogTerm,
      lastLogIndex: this._log.lastLogIndex,
    })

    this._log.configuration.forEachPeer(peerId => {
      if (!voteCollection.has(peerId)) {
        this._send(peerId, request)
      }
    })
  }

  handlePreVoteResponse(peerId, message) {
    if (!this._preVoteDone && message.term === this._term) {
      // We got a vote for the current term, so we store the reply and check if we
      // have received enough votes to have a majority and become leader
      this._preVotes.set(peerId, message.granted)
      log.debug(TAG, this, `received pre-vote reply from ${peerId}: ${message.granted ? 'granted' : 'rejected'}`)
      if (this._log.configuration.hasMajority(this._preVotes.grantedVotes)) {
        this._preVoteDone = true
        this._votes = new Votes(this._log.configuration.ownId)
        // trigger tick and reset timer
        this.stop()
        this.start()
      }
    }
  }

  handleRequestVoteResponse(peerId, message) {
    if (this._preVoteDone && message.term === this._term) {
      // We got a vote for the current term, so we store the reply and check if we
      // have received enough votes to have a majority and become leader
      this._votes.set(peerId, message.granted)
      log.debug(TAG, this, `received vote reply from ${peerId}: ${message.granted ? 'granted' : 'rejected'}`)
      if (this._log.configuration.hasMajority(this._votes.grantedVotes)) {
        return this.toLeader(this._term)
      }
    }
  }
}

/*
        ##  #######  #### ##    ## ######## ########
        ## ##     ##  ##  ###   ## ##       ##     ##
        ## ##     ##  ##  ####  ## ##       ##     ##
        ## ##     ##  ##  ## ## ## ######   ########
  ##    ## ##     ##  ##  ##  #### ##       ##   ##
  ##    ## ##     ##  ##  ##   ### ##       ##    ##
   ######   #######  #### ##    ## ######## ##     ##
*/

// The joiner state is a new state not documented in the main raft paper.
//
// In this state the client waits for a snapshot request to be able to join in, it
// then waits for a configuration to be committed where the client is a member of the cluster.
//
// When adding a peer, the leader will first send snaptshots until a snapshot response for
// the current term is received. It will then request a new configuration which includes the
// new client as soon as possible.
//
// Clients in the joiner state will not vote or be requested to vote, they will also not
// time out and start candidating.
export class JoinerState extends RaftState {
  constructor(log, send) {
    super(log, send, 0)
  }

  toString() {
    return 'joiner{}'
  }

  handleAppendEntriesRequest(peerId, message) {
    if (this._term === 0) {
      // We haven't received a snapshot yet. The old leader was probably replaced and the new one thinks
      // we're a regular cluster member.
      this._send(peerId, message.response({needSnapshot: true}))
      return
    }
    let response = this._log.handleAppendEntriesRequest(message)
    if (response) {
      this._send(peerId, response)
      this._term = message.term
      return this._convertToFollowerIfMember(peerId)
    }
  }

  handleAppendEntriesResponse(peerId, message) {
  }

  handleRequestVoteRequest(peerId, message) {
    // joiners do not participate in voting in any way
  }

  handleRequestVoteResponse(peerId, message) {
  }

  handleSnapshotRequest(peerId, message) {
    if (this._term > message.term || this._log.commitIndex > message.lastIndex) {
      return // ignore old snapshots
    }
    this._log.loadSnapshot(message)
    this._send(peerId, message.response())
    this._term = message.term
    return this._convertToFollowerIfMember(peerId)
  }

  handleSnapshotResponse(peerId, message) {
  }

  _convertToFollowerIfMember(peerId) {
    let configData = this._log.getCommittedConfigurationData()
    let ownId = this._log.configuration.ownId
    if (configData.includes(ownId)) {
      log.info(TAG, this, 'found itself in committed configuration, converting to follower')
      return this.toFollower(this._term, peerId)
    }
  }
}



