'use strict'

import log from 'common/log'
import MediaSource from 'media/mediaSource'
import {InputMap} from 'media/mediaNode'
import {audioContextUtil} from 'webrtc/utils'

const TAG = 'audiomixer'

/**
 * A media node that mixes the audio tracks of all input streams and ouputs a single audio stream.
 *
 * @class AudioMixer
 * @extends MediaSource
 */
export default class AudioMixer extends MediaSource {
  constructor() {
    super()
    this._inputs = new InputMap({
      parent: this,
      onStream: this._onStream.bind(this),
    })
    this._streamSources = {}
  }

  /**
   * An input map for all inputs to the node.
   *
   * @member {InputMap} AudioMixer#inputs
   */
  get inputs() {
    return this._inputs
  }

  _hasInputStreams() {
    return [...this.inputs.values()].some(input => input.stream)
  }

  _onStream(key, stream, oldStream) {
    if (oldStream) {
      this._streamSources[key].disconnect()
      delete this._streamSources[key]
      log.info(TAG, 'Stream removed:', key)
      if (this._dest && !this._hasInputStreams()) {
        this.setStream(null)
        log.info(TAG, 'Destroying output')
        this._stereoPanner.disconnect()
        this._stereoPanner = null
        this._dest = null
        audioContextUtil.release()
        this._audioContext = null
      }
    }

    if (stream) {
      if (!this._dest) {
        log.info(TAG, 'Creating output')
        this._audioContext = audioContextUtil.get()
        this._dest = this._audioContext.createMediaStreamDestination()
        this._stereoPanner = this._audioContext.createStereoPanner()
        this._stereoPanner.connect(this._dest)
        this.setStream(this._dest.stream)
      }
      let streamSource = this._audioContext.createMediaStreamSource(stream)
      streamSource.connect(this._stereoPanner)
      this._streamSources[key] = streamSource
      log.info(TAG, 'Stream added:', key)
    }
  }
}



