'use strict'

import argCheck from 'common/argCheck'

export default class Configuration {
  constructor({ownId} = {}) {
    this._ownId = argCheck.string(this, 'ownId', ownId)
    this._members = new Members()
  }

  toString() {
    return `config{members=${[...this._members].join(',')}}`
  }

  get ownId() {
    return this._ownId
  }

  get members() {
    return new Members(this._members)
  }

  createMembersChange(newMembers) {
    argCheck.instance('Configuration.createMembersChange', 'newMembers', newMembers, Members)
    return new ConfigurationData(newMembers)
  }

  get() {
    return new ConfigurationData(this._members)
  }

  set(data) {
    argCheck.instance('Configuration.set', 'data', data, ConfigurationData)
    this._members = data.members
  }

  // for each server, including self
  forEach(func, thisArg) {
    this.members.forEach(func, thisArg)
  }

  // for each peer in the cluster
  forEachPeer(func, thisArg) {
    this.members.forEach(peerId => {
      if (peerId !== this._ownId) {
        func.call(this.members, peerId)
      }
    }, thisArg)
  }

  hasMajority(grantedVotes) {
    return this._members.hasMajority(grantedVotes)
  }

  getMajorityIndex(matchIndices) {
    return this._members.getMajorityIndex(matchIndices)
  }
}

export class ConfigurationData {
  static parse(data) {
    let {members} = argCheck.object(this, 'data', data)
    return new ConfigurationData(new Members(members))
  }

  constructor(members) {
    argCheck.instance(this, 'members', members, Members)
    this._members = members
  }

  toString() {
    return `configData{members=${[...this._members].join(',')}}`
  }

  toJSON() {
    // Only polyfilled sets serialize to arrays, so be explicit
    return {members: [...this._members]}
  }

  get members() {
    return this._members
  }

  includes(id) {
    if (this._members.has(id)) {
      return true
    }
    return false
  }
}

export class Members extends Set {
  get majorityCount() {
    return ((this.size / 2) | 0) + 1
  }

  get minorityCount() {
    return ((this.size - 1) / 2) | 0
  }

  get majorityIndexPosition() {
    return ((this.size + 1) / 2) | 0
  }

  voteCount(grantedVotes /* Set */) {
    let voteCount = 0
    this.forEach(id => {
      if (grantedVotes.has(id)) {
        voteCount += 1
      }
    })
    return voteCount
  }

  hasMajority(grantedVotes /* Set */) {
    let voteCount = this.voteCount(grantedVotes)
    return voteCount >= this.majorityCount
  }

  // Finds the greatest log index that a majority is guaranteed to have received
  getMajorityIndex(matchIndices /* {[clientId]: matchIndex} */) {
    let indices = []
    this.forEach(id => {
      if (matchIndices.hasOwnProperty(id)) {
        indices.push(matchIndices[id])
      } else {
        indices.push(0)
      }
    })
    let majorityPosition = this.majorityIndexPosition
    if (indices.length === 0 || indices.length < majorityPosition) {
      return 0
    } else {
      return indices.sort()[majorityPosition - 1]
    }
  }
}

export class Votes {
  constructor(ownId) {
    this._votes = new Map()
    if (ownId) {
      this.set(ownId, true)
    }
  }

  toString() {
    return `votes{${[...this._votes].map(([peerId, granted]) => granted ? `>${peerId}<` : peerId)}}`
  }

  set(peerId, granted) {
    return this._votes.set(peerId, granted)
  }

  has(peerId) {
    return this._votes.has(peerId)
  }

  get grantedVotes() {
    let voters = new Set()
    this._votes.forEach((granted, peerId) => {
      if (granted) {
        voters.add(peerId)
      }
    })
    return voters
  }
}



