'use strict'

import {MediaStream} from 'webrtc/utils'

import MediaNodeInput from 'media/mediaNodeInput'
import MediaSource from 'media/mediaSource'

/**
 * A media node which merges the audio and video tracks from two different streams into one stream.
 *
 * @class StreamMerger
 * @extends MediaSource
 */
export default class StreamMerger extends MediaSource {
  constructor() {
    super()

    // Make sure we handle synchronous updates of both inputs as one update
    let microtask = null
    let onStream = () => {
      if (!microtask) {
        microtask = Promise.resolve().then(() => {
          microtask = null
          this.onStream()
        })
      }
    }
    this._audioInput = new MediaNodeInput({
      name: 'default',
      parent: this,
      onStream,
    })
    this._videoInput = new MediaNodeInput({
      name: 'default',
      parent: this,
      onStream,
    })
  }

  /**
   * The input that the audio track should be extracted from.
   *
   * @member {MediaNodeInput} StreamMerger#audioInput
   */
  get audioInput() {
    return this._audioInput
  }

  /**
   * The input that the video track should be extracted from.
   *
   * @member {MediaNodeInput} StreamMerger#videoInput
   */
  get videoInput() {
    return this._videoInput
  }

  /**
   * @private
   */
  onStream() {
    if (!this._audioInput.stream && !this._videoInput.stream) {
      this.setStream(null)
      return
    }
    let stream = new MediaStream()
    if (this._audioInput.stream) {
      this._audioInput.stream.getAudioTracks().forEach(track => {
        stream.addTrack(track)
      })
    }
    if (this._videoInput.stream) {
      this._videoInput.stream.getVideoTracks().forEach(track => {
        stream.addTrack(track)
      })
    }
    this.setStream(stream)
  }
}



