  let set2 = new Set(collection2)
  let added = []
  let removed = []
  set1.forEach(element => {
    if (!set2.has(element)) {
      removed.push(element)
    }
  })
  set2.forEach(element => {
    if (!set1.has(element)) {
      added.push(element)
    }
  })
  return [added, removed]
