export function createTimedBuffer(time, callback) {
  let args = []
  let timeout = 0
  return function (arg) {
    args.push(arg)
    if (!timeout) {
      timeout = setTimeout(onTimeout, time)
    }
    function onTimeout() {
      timeout = 0
      try {
        callback(args)
      } catch (error) {
        console.error(`error in timed buffer callback: ${error}`)
      }
      args = []
    }
  }
}

export function errorForwarder(handler, location) {
  return error => {
    if (!location) {
      handler(error)
    } else {
      let newError = new Error(`Error in ${location}: ${error.message}`)
      newError.reason = error
      handler(newError)
    }
  }
}

