'use strict'

import log from 'common/log'
import {ConnectionFailedError, ConnectionLostError} from 'webrtc/errors'
import {browser, randomString, errorForwarder} from 'common/utils'

import Candidater from 'webrtc/candidater'
import Renegotiator from 'webrtc/renegotiator'

const TAG = 'signaler'

// state of the WebRTC offer/answer signaling
const SIGNALING_STATE_IDLE = 'idle'
const SIGNALING_STATE_OFFERED = 'offered'
const SIGNALING_STATE_STABLE = 'stable'

/*
      .-------------+--------------.
      v             |              |
  signaling -> connecting ---> connected
      ^                          |   ^
      |                          v   |
      `--------------------- reconnecting

*/
// signaling, peer connection is not in stable state
const CONNECTION_STATE_SIGNALING = 'signaling'
// peer connection in stable state, ice not connected
const CONNECTION_STATE_CONNECTING = 'connecting'
// peer connection stable and ice connected
const CONNECTION_STATE_CONNECTED = 'connected'
// ice was connected but has disconnected, might recover
const CONNECTION_STATE_RECONNECTING = 'reconnecting'
// call has been closed
const CONNECTION_STATE_CLOSED = 'closed'

const DATACHANNEL_BANDWIDTH = 2048 // TODO: Too high and stuff will break, figure out the right number.
const DISCONNECT_TIMEOUT_MS = 5000
const SIGNALING_PROTOCOL_VERSION = 3

function createTicket(remoteTicket) {
  if (remoteTicket) {
    // we have a remote ticket already, need to
    // make sure our ticket is greater than it.
    return remoteTicket + '.'
  } else {
    return Date.now() + randomString(8)
  }
}

export default class Signaler {
  constructor(deps) {
    if (deps.receivedOffer) {
      this._ticket = deps.receivedOffer.ticket
      this._signalingState = null
    } else {
      this._ticket = null
      this._signalingState = SIGNALING_STATE_IDLE
    }
    this._label = deps.label
    this._isInitiator = null
    this._peerConnection = null
    this._receivedOffer = deps.receivedOffer
    this._disconnectedTicket = null

    this._errorState = null
    this._connectionState = CONNECTION_STATE_SIGNALING
    this._hasDataConnection = false
    this._connectionFailCount = 0
    this._disconnectTimeoutId = 0

    this._closeCall = deps.closeCall
    this._onMessage = deps.onMessage
    this._onSignalingError = deps.onSignalingError
    this._onErrorStateChange = deps.onErrorStateChange
    this._onConnectionStateChange = deps.onConnectionStateChange
    this._createPeerConnection = deps.onCreatePeerConnection

    this._renegotiator = new Renegotiator({
      label: this._label,
      onError: deps.onSignalingError,
      channel: deps.renegotiationChannel,
    })

    this._candidater = new Candidater({
      send: candidates => {
        this._sendMessage({candidates})
      },
    })
    this._onIceStateChange = this._onIceStateChange.bind(this)
  }

  _sendMessage(message, ticket = this._ticket) {
    return this._onMessage({
      ticket,
      version: SIGNALING_PROTOCOL_VERSION,
      ...message,
    })
  }

  handleDataChannelConnect() {
    log.debug(TAG, this, 'signaling channel opened')
    if (this.closed) {
      return log.debug(TAG, this, 'signaling channel opened in closed state')
    }
    this._hasDataConnection = true
    this._renegotiator.start(this._isInitiator)
    this._setConnectionState(CONNECTION_STATE_CONNECTED)
    this._setErrorState(null)
    this._connectionFailCount = 0
  }

  // 'closed' | 'channelTimeout' | 'connectTimeout'
  handleDataChannelDisconnect(reason) {
    this._hasDataConnection = false
    log.debug(TAG, this, `data connection lost because of ${reason}`)
    if (this._connectionState === CONNECTION_STATE_CONNECTED) {
      this._connectionHasFailed(new ConnectionLostError('The data connection was lost'))
    }
  }

  toString() {
    return `signaler{"${this._label}",state=${this._signalingState},` +
      `ticket=${this._ticket},isInitiator=${this._isInitiator}}`
  }

  get ticket() {
    return this._ticket
  }

  get connectionState() {
    return this._connectionState
  }

  get errorState() {
    return this._errorState
  }

  /* true, false, null - tribool wombo-combo ! */
  get isInitiator() {
    return this._isInitiator
  }

  isConnected() {
    return this._hasDataConnection
  }

  get closed() {
    return this._connectionState === CONNECTION_STATE_CLOSED
  }

  get stopped() {
    return !this._signalingState
  }

  transitionToClosed() {
    if (this.closed) {
      return false
    }
    this._setConnectionState(CONNECTION_STATE_CLOSED)
    return true
  }

  _replacePeerConnection() {
    if (this._peerConnection) {
      this._peerConnection.removeEventListener('iceconnectionstatechange', this._onIceStateChange)
    }
    this._peerConnection = this._createPeerConnection()
    this._peerConnection.addEventListener('iceconnectionstatechange', this._onIceStateChange)

    this._candidater.setPeerConnection(this._peerConnection)
    this._renegotiator.setPeerConnection(this._peerConnection)
  }

  _sendDescription(description, ticket) {
    let content = {
      browser,
      candidates: this._candidater.extractEarlyLocal(),
      [description.type]: {
        type: description.type,
        sdp: description.sdp,
        unifiedPlanSdp: description.unifiedPlanSdp,
      },
    }
    log.debug(TAG, this, `sending description, ${JSON.stringify(content)}`)
    this._sendMessage(content, ticket)
  }

  handleMessage(message) {
    if (this.closed) {
      return
    }
    log.debug(TAG, this, 'handling signaling message', message)
    let {ticket, hangup, offer, answer, candidates, version} = message
    if (version < SIGNALING_PROTOCOL_VERSION) {
      log.error(TAG, this, `ignoring message with incompatible protocol version: ${version}`)
      return
    }
    if (hangup) {
      if (!(ticket === this._ticket || ticket === this._disconnectedTicket)) {
        log.warning(TAG, this, `ignored hangup with wrong ticket: ${ticket}, old ticket = ${this._disconnectedTicket}`)
        return
      }
      throw new Error('hangup')
    }
    if (offer) {
      this._handleOffer(message)
    } else if (answer) {
      if (ticket !== this._ticket) {
        log.warning(TAG, this, `ignored answer with wrong ticket: ${ticket}`)
        return
      }
      this._handleAnswer(message)
    }
    if (candidates) {
      if (ticket !== this._ticket) {
        log.info(TAG, this, `ignored candidates with wrong ticket: ${ticket}`)
        return
      }
      this._candidater.handleRemoteCandidates(candidates)
    }
  }

  _setConnectionState(connectionState) {
    let prevState = this._connectionState
    this._connectionState = connectionState
    if (prevState !== connectionState) {
      this._onConnectionStateChange(connectionState, prevState)
    }
  }

  _setErrorState(errorState) {
    if (errorState !== this._errorState) {
      this._errorState = errorState
      this._onErrorStateChange(errorState)
    }
  }

  _connectionHasFailed(error) {
    clearTimeout(this._disconnectTimeoutId)
    this._disconnectTimeoutId = 0
    if (this.closed) {
      return
    }

    if (this._connectionState === CONNECTION_STATE_SIGNALING) {
      log.warning(TAG, this, 'connection failed while signaling')
    }
    this._setConnectionState(CONNECTION_STATE_SIGNALING)
    this._setErrorState(error)
    this._disconnectedTicket = this._ticket

    if (this.closed) {
      return // application closed the call in the error handler
    }
    log.info(TAG, this, 'trying to restart call because connection failed')
    // channel and ice listeners are cleared here, so this method won't be called twice for the same pc
    this._replacePeerConnection()
    this._doOffer()
  }

  _onIceStateChange() {
    if (this.closed) {
      return
    }
    var connState = this._peerConnection.iceConnectionState
    log.debug(TAG, this, `changed ice connection state to ${connState}`)

    if (connState === 'failed') {
      this._connectionFailCount += 1
      this._connectionHasFailed(new ConnectionFailedError(this._connectionFailCount))
    } else if (connState === 'disconnected') {
      this._setConnectionState(CONNECTION_STATE_RECONNECTING)
      this._disconnectTimeoutId = setTimeout(() => {
        this._connectionHasFailed(new ConnectionLostError('Timed out in disconnected state'))
      }, DISCONNECT_TIMEOUT_MS)
    } else {
      clearTimeout(this._disconnectTimeoutId)
      this._disconnectTimeoutId = 0
      if (connState === 'connected' && this.isConnected()) {
        this._setConnectionState(CONNECTION_STATE_CONNECTED)
      }
    }
  }

  _doOffer(remoteTicket) {
    if (this.closed) {
      return log.warning(TAG, this, 'trying to do offer in closed state')
    }
    // Need to make sure this ticket is used in the closure below. The _ticket member might
    // get updated because of a conflict, and then we would send the offer with
    // the wrong (new) ticket.
    let ticket = createTicket(remoteTicket)
    this._ticket = ticket
    log.info(TAG, this, `doing offer with ticket ${this._ticket}`)
    this._signalingState = SIGNALING_STATE_OFFERED
    this._isInitiator = true
    this._peerConnection.performOffer({bandwidth: DATACHANNEL_BANDWIDTH})
      .then(description => this._sendDescription(description, ticket))
      .catch(errorForwarder(this._onSignalingError, '_doOffer'))
  }

  _doAnswer(offer, ticket) {
    if (this.closed) {
      return log.warning(TAG, this, 'trying to do answer in closed state')
    }
    this._ticket = ticket
    log.info(TAG, this, `doing answer with ticket ${this._ticket}`)
    this._signalingState = SIGNALING_STATE_STABLE
    this._isInitiator = false
    this._peerConnection.performAnswer({offer, bandwidth: DATACHANNEL_BANDWIDTH})
      .then(description => this._sendDescription(description, ticket))
      .then(() => {
        this._candidater.addEarlyRemoteCandidates()
        this._signalingState = SIGNALING_STATE_STABLE
        if (this._connectionState !== CONNECTION_STATE_CONNECTED) {
          this._setConnectionState(CONNECTION_STATE_CONNECTING)
        }
      })
      .catch(errorForwarder(this._onSignalingError, '_doAnswer'))
  }

  _handleOffer({offer, ticket, browser: remoteBrowser, candidates}) {
    // this.candidater.handleRemoteCandidates(candidates)
    var alwaysOffer = false
    log.debug(TAG, this, `got offer in state '${this._signalingState}' with tickets ${ticket} >= ${this._ticket}`)
    let isChromeToOther = browser === 'chrome' && remoteBrowser !== 'chrome'
    let isPluginToFirefox = browser === 'other' && remoteBrowser === 'firefox'
    if (isChromeToOther || isPluginToFirefox) {
      log.debug(TAG, `offer is from ${remoteBrowser} and this is ${browser}, forcing offer`)
      alwaysOffer = true
    }
    if (this._signalingState === SIGNALING_STATE_IDLE) {
      this._replacePeerConnection()
      if (alwaysOffer) {
        this._doOffer(ticket)
      } else {
        log.info(TAG, this, 'restarting call and applying offer')
        this._doAnswer(offer, ticket)
      }
    } else if (this._signalingState === SIGNALING_STATE_OFFERED) {
      if (ticket >= this._ticket) { // call needs to be aborted
        this._replacePeerConnection()
        if (alwaysOffer) {
          log.info(TAG, this, 'resolving always offer conflict by resetting and offering')
          this._doOffer(ticket)
        } else {
          log.info(TAG, this, 'resolving conflict by aborting dial out and sending answer')
          this._doAnswer(offer, ticket)
        }
      } else {
        log.debug(TAG, this, 'resolving conflict by waiting for answer')
      }
    } else if (this._signalingState === SIGNALING_STATE_STABLE) {
      if (alwaysOffer) {
        log.warning(TAG, this, 'resetting call and offering')
        this._replacePeerConnection()
        this._doOffer(ticket)
      } else if (ticket >= this._ticket) { // call should be reset
        log.info(TAG, this, 'resetting call and applying offer')
        this._replacePeerConnection()
        this._doAnswer(offer, ticket)
      } else {
        log.debug(TAG, this, 'ignoring offer for old call')
      }
    } else if (this.closed) {
      log.info(TAG, this, 'got offer in closed state, ignoring')
    }
  }

  _handleAnswer({answer, ticket}) {
    if (this._signalingState !== SIGNALING_STATE_OFFERED) {
      return log.error(TAG, this, `got answer in wrong state: ${this._signalingState}`)
    }
    this._signalingState = SIGNALING_STATE_STABLE
    log.verbose(TAG, this, 'got answer, initial call setup stabilized')

    this._peerConnection.handleAnswer({answer})
      .then(() => {
        this._candidater.addEarlyRemoteCandidates()
        if (this._connectionState !== CONNECTION_STATE_CONNECTED) {
          this._setConnectionState(CONNECTION_STATE_CONNECTING)
        }
      })
      .catch(errorForwarder(this._onSignalingError, '_handleAnswer'))
  }

  sendHangup() {
    this._sendMessage({hangup: true})
  }

  start() {
    if (this.closed) {
      return false
    }

    if (this._receivedOffer) {
      log.debug(TAG, this, 'accepting incoming call:', this._receivedOffer)
      this._signalingState = SIGNALING_STATE_IDLE
      this._handleOffer(this._receivedOffer)
      this._receivedOffer = null
    } else {
      log.info(TAG, `call started or restarted in state '${this._signalingState}'`)
      this._signalingState = SIGNALING_STATE_OFFERED
      this._replacePeerConnection()
      setTimeout(() => this._doOffer())
    }
    return true
  }

  reset() {
    if (this._peerConnection) {
      this._peerConnection.removeEventListener('iceconnectionstatechange', this._onIceStateChange)
    }
    clearTimeout(this._disconnectTimeoutId)
    this._disconnectTimeoutId = 0
    this._hasDataConnection = false
    this._peerConnection = null
    this._isInitiator = null
    this._renegotiator.reset()
    this._candidater.reset()
  }
}



