
import log from 'common/log'

const TAG = 'peer-connection'

const candidateRegExp = new RegExp('^candidate:(\\d+) (\\d) (UDP|TCP) ([\\d\\.]*) ([\\d\\.a-f\\:]*) (\\d*)' +
  ' typ ([a-z]*)( raddr ([\\d\\.a-f\\:]*) rport (\\d*))?' +
  '( tcptype (active|passive|so))?(.*)$', 'i')
const bundleAttributeRegExp = new RegExp('^a=group:BUNDLE (.*)$', 'mi')

/**
 * @typedef IceCandidateInfo
 *
 * An object containing information about an ICE candidate.
 *
 * More information can be found in [RFC 5245](https://tools.ietf.org/html/rfc5245) and
 * [RFC 6544](https://tools.ietf.org/html/rfc6544).
 *
 * @property {string} foundation - The candidate foundation.
 * @property {number} componentId - The candidate component id, will almost always be 1 because of RTCP multiplexing.
 * @property {string} transport - The transport used, `'TCP'` or `'UDP'`.
 * @property {number} priority - The candidate priority.
 * @property {string} address - The candidate address.
 * @property {number} port - The candidate port.
 * @property {string} type - The candidate type, `'host'`, `'srflx'`, `'prflx'`, or `'relay'`.
 * @property {string} candidate - The original candidate line, always without attribute prefix (`a=`).
 * @property {string} origin - The origin of the candidate, `'local'` or `'remote'`.
 * @property {string} [relatedAddress] - The candidate related address, not used in host candidates.
 * @property {number} [relatedPort] - The candidate related port, not used in host candidates.
 * @property {string} [tcpType] - The candidate tcp type, `'active'`, `'passive'`, simultaneous-open, `'so'`.
 */

export function parseCandidateInfo(line, origin) {
  let match = line.match(candidateRegExp)
  if (!match) {
    throw new Error(`Could not parse ${origin} ice candidate: ${line}`)
  }
  let info = {
    foundation: match[1],
    componentId: parseInt(match[2]),
    transport: match[3].toUpperCase(),
    priority: parseInt(match[4]),
    address: match[5],
    port: parseInt(match[6]),
    type: match[7],
    candidate: line,
    origin: origin,
  }
  if (match[9]) {
    info.relatedAddress = match[9]
  }
  if (!isNaN(match[10])) {
    info.relatedPort = parseInt(match[10])
  }
  if (match[12]) {
    info.tcpType = match[12]
  } else if (info.transport === 'TCP') {
    if (info.port === 0 || info.port === 9) {
      info.tcpType = 'active'
      info.port = 9
    } else {
      return
    }
  }
  return info
}

export function filterSdpCandidates(sdp, filter, origin) {
  if (!sdp) {
    return sdp
  }
  return sdp.split(/\r\n/).filter(line => {
    if (line.indexOf('a=candidate:') === 0) {
      try {
        let candidate = line.slice(2)
        let info = parseCandidateInfo(candidate, origin)
        if (!filter(info)) {
          log.verbose(TAG, `ignored candidate due to being filtered out: ${candidate}`)
          return false
        }
      } catch (error) {
        log.error(TAG, `ignoring candidate, error was thrown in candidate filter: ${error}`)
        return false
      }
    }
    return true
  }).join('\r\n')
}

export function workaroundSdpBundleBug(sdp) {
  if (sdp.match(bundleAttributeRegExp)) {
    // If the SDP already has a group attribute, we don't need to do anything
    return sdp
  }
  return rewriteBundleAttribute(sdp)
}

export function extractApplicationBlock(sdp) {
  let [sessionBlock, ...mediaBlocks] = sdp.split(blockSplitRegExp)

  let applicationMBlockIndex = -1
  for (let index in mediaBlocks) {
    if (mediaBlocks[index].indexOf('m=application') === 0) {
      applicationMBlockIndex = +index
      break
    }
  }

  if (applicationMBlockIndex === -1) {
    return [sdp, null]
  }

  let [, applicationMId] = mediaBlocks[applicationMBlockIndex].match(/a=mid:(.*)/mi) || []
  if (!applicationMId) {
    throw new Error('sdp application block does not have an mId')
  }

  let newSessionBlock = sessionBlock.replace(bundleAttributeRegExp, (_, mIdsStr) => {
    let mIds = mIdsStr.split(' ')
    let index = mIds.indexOf(applicationMId)
    if (index === -1) {
      throw new Error('sdp group:BUNDLE attribute did not contain application block mId')
    }
    mIds.splice(index, 1)
    return 'a=group:BUNDLE ' + mIds.join(' ')
  })

  let applicationMBlock = mediaBlocks.splice(applicationMBlockIndex, 1)[0]
  let newSdp = newSessionBlock + mediaBlocks.join('')
  return [newSdp, {
    block: applicationMBlock,
    index: applicationMBlockIndex,
    applicationMId,
    needsInteropTranslation: !!mediaBlocks.length,
  }]
}

export function injectApplicationBlock(sdp, extractedBlock) {
  if (!extractedBlock) {
    return sdp
  }

  let [sessionBlock, ...mediaBlocks] = sdp.split(blockSplitRegExp)

  let newSessionBlock = sessionBlock.replace(bundleAttributeRegExp, (_, mIdsStr) => {
    let mIds = mIdsStr ? mIdsStr.split(' ') : []
    mIds.splice(extractedBlock.index, 0, extractedBlock.applicationMId)
    return 'a=group:BUNDLE ' + mIds.join(' ')
  })

  mediaBlocks.splice(extractedBlock.index, 0, extractedBlock.block)
  return newSessionBlock + mediaBlocks.join('')
}

const modeRegExp = new RegExp('^a=(inactive|recvonly|sendonly|sendrecv)$', 'mi')
const blockSplitRegExp = new RegExp('(?=m=(?:audio|video|application))', 'i')
const mediaTypeRegExp = new RegExp('^m=(audio|video|application)', 'i')

const modeValues = {
  inactive: 0,
  sendonly: 1,
  recvonly: 2,
  sendrecv: 3,
}
const modeNames = {
  0: 'inactive',
  1: 'sendonly',
  2: 'recvonly',
  3: 'sendrecv',
}

export function readMediaModeUnions(sdp) {
  let [sessionBlock, ...mediaBlocks] = sdp.split(blockSplitRegExp)
  // the mode above the media blocks is used if a media block doesn't have it's own mode
  let [, defaultModeName = 'sendrecv'] = sessionBlock.match(modeRegExp) || []

  let audioModeValue = modeValues.inactive
  let videoModeValue = modeValues.inactive
  let applicationModeValue = modeValues.inactive

  mediaBlocks.forEach(mediaBlock => {
    let [, mediaType] = mediaBlock.match(mediaTypeRegExp)
    let [, modeName = defaultModeName] = mediaBlock.match(modeRegExp) || []
    let modeValue = modeValues[modeName]
    if (mediaType === 'audio') {
      audioModeValue |= modeValue
    } else if (mediaType === 'video') {
      videoModeValue |= modeValue
    } else if (mediaType === 'application') {
      applicationModeValue |= modeValue
    }
  })

  return {
    audio: modeNames[audioModeValue],
    video: modeNames[videoModeValue],
    application: modeNames[applicationModeValue],
  }
}

export function overrideMediaModes(sdp, mediaModes) {
  let [sessionBlock, ...mediaBlocks] = sdp.split(blockSplitRegExp)

  mediaBlocks = mediaBlocks.map(mediaBlock => {
    let [, mediaType] = mediaBlock.match(mediaTypeRegExp)
    let modeName = mediaModes[mediaType] || modeNames[3] // default to sendrecv
    return mediaBlock.replace(modeRegExp, `a=${modeName}`)
  })

  return [sessionBlock, ...mediaBlocks].join('')
}

// This rewrites the bundle attribute according to the actual media blocks in the sdp
export function rewriteBundleAttribute(sdp) {
  let [sessionBlock, ...mediaBlocks] = sdp.split(blockSplitRegExp)
  let mIds = mediaBlocks.map(mediaBlock => {
    let [, mId] = mediaBlock.match(/^a=mid:(.+)$/mi) || []
    if (!mId) {
      let [, mediaType] = mediaBlock.match(mediaTypeRegExp)
      throw new Error(`media block of type ${mediaType} did not contain mid attribute`)
    }
    return mId
  })

  mIds.unshift('') // conditional padding weith space after BUNDLE
  let bundleAttribute = `a=group:BUNDLE${mIds.join(' ')}`

  if (sessionBlock.match(bundleAttributeRegExp)) {
    sessionBlock = sessionBlock.replace(bundleAttributeRegExp, bundleAttribute)
  } else {
    sessionBlock += `${bundleAttribute}\r\n`
  }
  return [sessionBlock, ...mediaBlocks].join('')
}

const bweRegExp = new RegExp('a=extmap:5 ' +
  'http://www\\.ietf\\.org/id/draft-holmer-rmcat-transport-wide-cc-extensions-01' +
  '\r\n', 'gi')

// Chrome 58 and 59 has a broken bandwidth estimation implementation that
// doesn't detect when packets are sent using TURN/TCP. This results in
// the estimated bandwidth to always end up at 10kbps.
// The fix we use is to always disable the new bandwidth estimation algorithm
// for all connections.
export function disableSendBandwidthEstimation(sdp) {
  return sdp.replace(bweRegExp, '')
}

const ssrcGroupRegExp = new RegExp('a=ssrc-group:.+\\s\\d+\\s(\\d+)' + '\r\n', 'gi')
const rtxPayloadTypeRegExp = new RegExp('a=(?:fmtp:\\d+\\sapt=\\d+|rtpmap:\\d+\\srtx/\\d+)' + '\r\n', 'gi')

// Removes retransmission information from a Chrome-style (Plan B) SDPs.
// This includes rtpmap and fmtp attributes with rtx/apt values, ssrc-group
// attributes, and the ssrc attributes for the ssrcs used for retransmission.
export function removeChromeRetransmission(sdp) {
  let [sessionBlock, ...mediaBlocks] = sdp.split(blockSplitRegExp)

  let modifiedMediaBlocks = mediaBlocks.map(mediaBlock => {
    let [, mediaType] = mediaBlock.match(mediaTypeRegExp)
    if (mediaType !== 'video') {
      return mediaBlock
    }
    let removedSsrcs = []

    let withoutSsrcGroups = mediaBlock.replace(ssrcGroupRegExp, (_, ssrc) => {
      // We assume the second SSRC is used for retransmission
      removedSsrcs.push(ssrc)
      return ''
    })
    let ssrcRegExp = new RegExp(`a=ssrc:(?:${removedSsrcs.join('|')})\\s.*\r\n`, 'gi')
    let withoutSsrcs = withoutSsrcGroups.replace(ssrcRegExp, '')
    let withoutRtxPayloadTypes = withoutSsrcs.replace(rtxPayloadTypeRegExp, '')
    return withoutRtxPayloadTypes
  })

  return [sessionBlock, ...modifiedMediaBlocks].join('')
}



