'use strict'

import argCheck from 'common/argCheck'
import log from 'common/log'
import {forEach, map} from 'common/utils'

const TAG = 'mesh-topology'

export class LinkInfo {
  constructor({rttMs = -1, bandwidth = -1, up = false, onUpdate}) {
    this._rttMs = argCheck.number(this, 'rttMs', rttMs)
    this._bandwidth = argCheck.number(this, 'bandwidth', bandwidth)
    this._up = argCheck.boolean(this, 'up', up)
    this._onUpdate = onUpdate
  }

  get up() {
    return this._up
  }

  get rttMs() {
    return this._rttMs
  }

  get bandwidth() {
    return this._bandwidth
  }

  update(info) {
    let {rttMs = null, bandwidth = null, up = null} = info
    if (rttMs !== null) {
      this._rttMs = argCheck.number('LinkInfo.update', 'rttMs', rttMs)
    }
    if (bandwidth !== null) {
      this._bandwidth = argCheck.number('LinkInfo.update', 'bandwidth', bandwidth)
    }
    let oldUp = this._up
    if (up !== null) {
      this._up = argCheck.boolean('LinkInfo.update', 'up', up)
    }
    if (this._onUpdate) {
      let changedUpState = oldUp !== this._up
      this._onUpdate(changedUpState)
    }
  }

  toString() {
    return `link{${this._up ? 'up' : 'down'},rttMs=${this._rttMs},bw=${this._bandwidth}}`
  }

  toJSON() {
    if (this._up) {
      return [this._rttMs, this._bandwidth]
    } else {
      return []
    }
  }

  static parse(data) {
    if (data.constructor === LinkInfo) {
      return data
    } else if (Array.isArray(data)) {
      if (!data.length) {
        return new LinkInfo({up: false})
      }
      let [rttMs, bandwidth] = data
      return new LinkInfo({rttMs, bandwidth, up: true})
    } else {
      throw new TypeError(`Could not parse link info data, ${data}`)
    }
  }
}

export class LinkInfos {
  static parse(data) {
    let linkInfos = new LinkInfos()
    forEach(data, (linkInfo, peerId) => {
      linkInfos[peerId] = LinkInfo.parse(linkInfo)
    })
    return linkInfos
  }

  toString() {
    return `linkInfos{${Object.keys(this).map(peerId => `${peerId}:${this[peerId]}`).join(',')}}`
  }
}

const INDEX_UP_STATE_CHANGE_SIZE = 10

export class PeerTopologyInfo {
  static parseUpdate(data) {
    if (!Array.isArray(data)) {
      return data
    }
    let [power, links] = data
    return {
      power,
      links: LinkInfos.parse(links),
    }
  }

  constructor(peerId, onChange) {
    this._peerId = peerId
    this._onChange = onChange
    this._topologyIndex = 1

    this._links = new LinkInfos()
    this._power = 1
  }

  toString() {
    return `peerTopologyInfo{peerId=${this._peerId},power=${this._power},${this._links}}`
  }

  toJSON() {
    return [this._power, this._links]
  }

  get links() {
    return this._links
  }

  get power() {
    return this._power
  }

  get volume() {
    return this._volume
  }

  get topologyIndex() {
    return this._topologyIndex
  }

  _update({topologyInfo, topologyIndex, volume}) {
    this._volume = volume
    if (!topologyInfo) {
      return
    }
    argCheck.number('PeerTopologyInfo._update', 'topologyIndex', topologyIndex)
    if (topologyIndex < this._topologyIndex) {
      return
    }
    argCheck.options('PeerTopologyInfo._update', 'topologyInfo', topologyInfo)
      .number('power')
      .instance('links', LinkInfos)
    this._links = topologyInfo.links
    this._power = topologyInfo.power
    let indexChange = topologyIndex - this._topologyIndex
    this._topologyIndex = topologyIndex
    log.verbose(TAG, this, `updated peer topology info by ${indexChange} indices`)
    this._onChange(indexChange >= INDEX_UP_STATE_CHANGE_SIZE)
  }

  _invalidate() {
    this._links = null
    this._onChange = null
  }
}

export class LocalTopologyInfo {
  constructor({power, voiceActivityDetector, onUpdate}) {
    this._handleUpdate = this._handleUpdate.bind(this)

    this._links = new LinkInfos()
    this._power = power
    this._voiceActivityDetector = voiceActivityDetector
    this._topologyIndex = 1
    this._onUpdate = onUpdate
  }

  get links() {
    return this._links
  }

  get power() {
    return this._power
  }

  get topologyIndex() {
    return this._topologyIndex
  }

  get volume() {
    if (this._voiceActivityDetector) {
      return this._voiceActivityDetector.volume
    } else {
      return 0
    }
  }

  setVoiceActivityDetectorActive(active) {
    if (this._voiceActivityDetector) {
      this._voiceActivityDetector.setActive(active)
    }
  }

  addPeer(peerId) {
    if (this._links.hasOwnProperty(peerId)) {
      throw new Error(`LocalTopologyInfo already has a peer with id ${peerId}`)
    }
    let linkInfo = new LinkInfo({onUpdate: this._handleUpdate})
    this._links[peerId] = linkInfo
    return linkInfo
  }

  removePeer(peerId) {
    let linkInfo = this._links[peerId]
    if (!linkInfo) {
      throw new Error(`LocalTopologyInfo failed to delete nonexistent peer, ${peerId}`)
    }
    delete this._links[peerId]
  }

  toString() {
    let infoStrings = map(this._links, (linkInfo, peerId) => {
      return `${peerId}:${linkInfo}`
    })
    return `localTopology{power=${this._power},${infoStrings.join(',')}}`
  }

  toJSON() {
    return [this._power, this._links]
  }

  _handleUpdate(changedUpState) {
    this._topologyIndex += changedUpState ? INDEX_UP_STATE_CHANGE_SIZE : 1
    this._onUpdate(changedUpState)
  }

  _invalidate() {
  }
}

export default class MeshTopology {
  constructor({ownId, power = 1, voiceActivityDetector = null}) {
    this._onChange = this._onChange.bind(this)

    this._localTopologyInfo = new LocalTopologyInfo({
      power,
      voiceActivityDetector,
      onUpdate: this._onLocalTopologyUpdate.bind(this),
    })
    this._topologyInfos = new Map()
    this._changeListeners = new Set()
    this._ownId = ownId
    this._active = false
  }

  toString() {
    if (this._topologyInfos.size === 0) {
      return `meshTopology{ownId=${this._ownId},${this._localTopologyInfo}}`
    }
    return `meshTopology{ownId=${this._ownId},${[...this._topologyInfos.values()].join(',')}}`
  }

  get topologyInfos() {
    return this._topologyInfos
  }

  get localTopologyInfo() {
    return this._localTopologyInfo
  }

  get active() {
    return this._active
  }

  // The mesh topology instance is only active when in the leader state
  // Being active means that it is possible to add, remove, and update peer link info,
  // and that changes will be signaled through the change listener
  activate() {
    log.debug(TAG, `activating ${this}`)
    this._active = true
    this._topologyInfos.clear()
    this._topologyInfos.set(this._ownId, this._localTopologyInfo)
    this._onChange(true)
  }

  deactivate() {
    log.debug(TAG, `deactivating ${this}`)
    this._active = false
    this._topologyInfos.forEach(peerTopologyInfo => peerTopologyInfo._invalidate())
    this._topologyInfos.clear()
    this._onChange(true)
  }

  invalidate() {
    this._changeListeners.clear()
    this.deactivate() // change listeners will not be called, since we cleared them
    this._changeListeners = null
    this._ownId = null
    this._localTopologyInfo._invalidate()
    this._localTopologyInfo = null
    this._topologyInfos = null
  }

  addClient(peerId) {
    if (!this._active) {
      throw new Error('Tried to add client while MeshTopology was deactivated')
    }
    if (this._topologyInfos.has(peerId)) {
      throw new Error('Tried to add existing client to MeshTopology')
    }
    let peerTopologyInfo = new PeerTopologyInfo(peerId, this._onChange)
    this._topologyInfos.set(peerId, peerTopologyInfo)
    log.debug(TAG, this, `adding client: ${peerId},  ${peerTopologyInfo}`)
    this._onChange(false) // links will be added later
    return peerTopologyInfo
  }

  removeClient(peerId) {
    if (!this._active) {
      throw new Error('Tried to remove client while MeshTopology was deactivated')
    }
    if (!this._topologyInfos.has(peerId)) {
      throw new Error('Tried to remove missing client from MeshTopology')
    }
    this._topologyInfos.delete(peerId)
    log.debug(TAG, this, `removed client: ${peerId}`)
    this._onChange(true)
  }

  addChangeListener(listener) {
    this._changeListeners.add(listener)
  }

  removeChangeListener(listener) {
    this._changeListeners.delete(listener)
  }

  _onChange(changedUpState) {
    this._changeListeners.forEach(listener => listener(this, changedUpState))
  }

  _onLocalTopologyUpdate(changedUpState) {
    this._onChange(changedUpState)
  }
}



