'use strict'

import log from 'common/log'
import argCheck from 'common/argCheck'
import {isFirefox, isChrome} from 'common/utils'

import {audioContextUtil} from 'webrtc/utils'

const TAG = 'voice-detector'

const MODE_STATS = 'stats'
const MODE_MAX = 'max'
const MODE_FFT = 'fft'
const MODE_RMS = 'rms'
const MODES = [MODE_STATS, MODE_MAX, MODE_FFT, MODE_RMS]

const TICK_INTERVAL_MS = 500

const VOLUME_FILTER_ALPHA = 0.05
const VOLUME_FILTER_INTERVAL_MS = 100

const RMS_ALPHA = 0.2

export class RmsVolumeAlgorithm {
  constructor() {
    this._filtered = 0
  }

  process(buffer) {
    var count = buffer.length
    var sumOfSquares = 0
    for (let i = 0; i < count; i += 1) {
      let val = buffer[i]
      sumOfSquares += val * val
    }
    let meanOfSquares = sumOfSquares / count
    let value = Math.sqrt(meanOfSquares)
    this._filtered = this._filtered * (1 - RMS_ALPHA) + value * RMS_ALPHA
    return this._filtered
  }
}

export class MaxVolumeAlgorithm {
  constructor() {
    this._max = 0
    this._volume = 0
    this._updateTime = 0

    this._updateFrequencyMs = 100
  }

  process(buffer) {
    var count = buffer.length
    let localMax = 0
    for (let i = 0; i < count; i += 1) {
      let val = buffer[i]
      if (val < -localMax) {
        localMax = -val
      } else if (val > localMax) {
        localMax = val
      }
    }

    if (localMax > this._max) {
      this._max = localMax
    }

    let now = Date.now()
    if (now - this._updateTime > this._updateFrequencyMs) {
      this._updateTime = now

      this._volume = this._max
      this._max /= 4 // Decay
    }

    return this._volume
  }
}

export class AudioProcessorMeter {
  constructor(algorithm) {
    this._processAudioData = this._processAudioData.bind(this)

    if (algorithm === MODE_RMS) {
      this._algorithm = new RmsVolumeAlgorithm()
    } else if (algorithm === MODE_MAX) {
      this._algorithm = new MaxVolumeAlgorithm()
    } else {
      throw new TypeError(`Unknown volume algorithm: '${algorithm}'`)
    }
    this._stream = null
    this._volume = 0
  }

  toString() {
    return `audioProcessorMeter{mode=${this._mode},volume=${this.volume}}`
  }

  get volume() {
    if (this._volume > 1) {
      return 1
    }
    return this._volume
  }

  tick(peerConnection, track, stream) {
    if (stream === this._stream) {
      return
    }

    if (this._stream) {
      if (this._audioSource) {
        this._audioSource.disconnect()
        this._audioSource = null
      }
      this._stream = null

      if (!stream) {
        if (this._processorNode) {
          this._processorNode.disconnect()
          this._processorNode = null
        }
        if (this._audioContext) {
          audioContextUtil.release()
          this._audioContext = null
          this._destination = null
        }
      }
    }
    this._stream = stream
    if (stream) {
      if (!this._audioContext) {
        this._audioContext = audioContextUtil.get()
        this._destination = this._audioContext.createMediaStreamDestination()
      }
      if (!this._processorNode) {
        this._processorNode = this._audioContext.createScriptProcessor(2048, 1, 1)
        this._processorNode.connect(this._destination)
        this._processorNode.onaudioprocess = this._processAudioData
      }

      this._audioSource = this._audioContext.createMediaStreamSource(stream)
      this._audioSource.connect(this._processorNode)
    }
  }

  _processAudioData(event) {
    if (!this._stream) {
      this._volume = 0
      return
    }
    let buffer = event.inputBuffer.getChannelData(0)
    this._volume = this._algorithm.process(buffer)
  }
}

export class RtcStatsMeter {
  constructor() {
    this._processStatsResult = this._processStatsResult.bind(this)
    this._volume = 0
  }

  toString() {
    return `rtcStatsMeter{volume=${this.volume}}`
  }

  get volume() {
    return this._volume
  }

  tick(peerConnection, track, stream) {
    if (!peerConnection) {
      return
    }
    if (isChrome) {
      peerConnection._pc.getStats(this._processStatsResult, track)
    } else {
      peerConnection._pc.getStats(track, this._processStatsResult, error => {
        log.error(TAG, this, `failed to get stats, ${error}`)
      })
    }
  }

  _processStatsResult(result) {
    let maxVolume = 0
    // We use the legacy Chrome API if available, since we then avoid the conversion to a Map.
    if (typeof(result.result) === 'function') {
      result.result().filter(stat => stat.type === 'ssrc').forEach(stat => {
        let volume = +stat.stat('audioInputLevel')
        if (volume > maxVolume) {
          maxVolume = volume
        }
      })
    } else {
      for (let key in result) {
        if (result.hasOwnProperty(key)) {
          let stat = result[key]
          if (stat.type === 'ssrc') {
            let volume = +stat.audioInputLevel
            if (volume > maxVolume) {
              maxVolume = volume
            }
          }
        }
      }
    }
    // WebRTC.org uses the absolute value of 16-bit signed intergers when calculating
    // audio levels, so divide by 0x8000
    let normalizedVolume = maxVolume / 0x8000
    this._volume = normalizedVolume
  }
}

class VolumeFilter {
  constructor({alpha, intervalMs, volumeSource}) {
    this._tick = this._tick.bind(this)

    this._alpha = alpha
    this._intervalMs = intervalMs
    this._volumeSource = volumeSource

    this._volume = 0
    this._intervalId = null
  }

  get volume() {
    return this._volume
  }

  start() {
    this.stop()
    this._intervalId = setInterval(this._tick, this._intervalMs)
  }

  stop() {
    this._volume = 0
    if (this._intervalId) {
      clearInterval(this._intervalId)
      this._intervalId = null
    }
  }

  _tick() {
    let measuredVolume = this._volumeSource.volume
    if (measuredVolume > 1) {
      measuredVolume = 1
    }
    let oldComponent = (1 - this._alpha) * this._volume
    let newComponent = this._alpha * measuredVolume
    this._volume = oldComponent + newComponent
  }
}

export default class VoiceActivityDetector {
  constructor({mode = null, active = false} = {}) {
    this._tick = this._tick.bind(this)

    argCheck.optValues(this, 'options.mode', mode, MODES)
    if (!mode) {
      if (isFirefox) {
        mode = MODE_MAX
      } else {
        mode = MODE_STATS
      }
    }

    if (mode === MODE_STATS) {
      this._meter = new RtcStatsMeter()
    } else if (mode === MODE_RMS || mode === MODE_MAX) {
      this._meter = new AudioProcessorMeter(mode)
    } else {
      log.error(TAG, `VoiceActivityDetector mode '${mode}' is not implemented`)
    }

    this._volumeFilter = new VolumeFilter({
      alpha: VOLUME_FILTER_ALPHA,
      intervalMs: VOLUME_FILTER_INTERVAL_MS,
      volumeSource: this._meter,
    })

    this._active = active

    this._peerConnection = null
    this._intervalId = null
  }

  toString() {
    return `voiceActivityDetector{meter=${this._meter}}`
  }

  get volume() {
    return this._volumeFilter.volume
  }

  setActive(active) {
    this._active = active
    this._maybeStartOrStop()
  }

  setPeerConnection(peerConnection) {
    this._peerConnection = peerConnection || null
    this._maybeStartOrStop()
  }

  _maybeStartOrStop() {
    if (this._peerConnection && this._active) {
      if (!this._intervalId) {
        this._intervalId = setInterval(this._tick, TICK_INTERVAL_MS)
        this._tick()
      }
      this._volumeFilter.start()
    } else if (!this._peerConnection && !this._active) {
      this._volumeFilter.stop()
      if (this._intervalId) {
        clearInterval(this._intervalId)
        this._intervalId = null
        this._meter.tick(null, null, null)
      }
    }
  }

  _findTrack() {
    var streams
    try {
      streams = this._peerConnection.getLocalStreams()
    } catch (error) {
      streams = []
      log.debug(TAG, `Failed to get local streams, ${error}`)
    }
    var track = null
    var stream = null
    for (let i = streams.length - 1; i >= 0; i -= 1) {
      let tracks = streams[i].getAudioTracks()
      track = tracks[tracks.length - 1]
      if (track) {
        stream = streams[i]
        break
      }
    }
    return [track, stream]
  }

  _tick() {
    let [track, stream] = this._findTrack()
    this._meter.tick(this._peerConnection, track, stream)
  }
}



