'use strict'

import {MediaStream} from 'webrtc/utils'

import MediaNodeOutput from 'media/mediaNodeOutput'
import MediaSink from 'media/mediaSink'

/**
 * A media node that splits a single media stream into one stream with the audio track, and another stream
 * with the video track.
 *
 * @class StreamSplitter
 * @extends MediaSink
 */
export default class StreamSplitter extends MediaSink {
  constructor() {
    super()
    this._audioOutput = new MediaNodeOutput({
      name: 'audio',
      parent: this,
    })
    this._videoOutput = new MediaNodeOutput({
      name: 'video',
      parent: this,
    })
  }

  /**
   * The output that will contain the audio track.
   *
   * @readonly
   * @member {MediaNodeOutput} StreamSplitter#audioOutput
   */
  get audioOutput() {
    return this._audioOutput
  }

  /**
   * The output that will contain the video track.
   *
   * @readonly
   * @member {MediaNodeOutput} StreamSplitter#videoOutput
   */
  get videoOutput() {
    return this._videoOutput
  }

  // eslint-disable-next-line
  /**
   * @private
   */
  onStream(stream) {
    if (!stream) {
      this._audioOutput.setStream(null)
      this._videoOutput.setStream(null)
      return
    }

    if (stream.getAudioTracks().length) {
      let newStream = new MediaStream()
      newStream.addTrack(stream.getAudioTracks()[0])
      this._audioOutput.setStream(newStream)
    } else {
      this._audioOutput.setStream(null)
    }

    if (stream.getVideoTracks().length) {
      let newStream = new MediaStream()
      newStream.addTrack(stream.getVideoTracks()[0])
      this._videoOutput.setStream(newStream)
    } else {
      this._videoOutput.setStream(null)
    }
  }
}



