'use strict'

import log from 'common/log'
import argCheck from 'common/argCheck'
import {defer, priv} from 'common/utils'
import Room from 'matrix/room'

// How long we'll wait for the initial m.room.create
const ROOM_CREATE_TIMEOUT = 20000
// How long we'll wait after the initial m.room.create until all events are received
const ROOM_PREPARE_TIMEOUT = 30000

const TAG = 'room-registry'

// dependencies.delegate._onRoomEvent
// dependencies.delegate._onRoomCreated
export default class RoomRegistry {
  constructor(dependencies) {
    this._roomMap = {}
    this._roomArray = []
    this._deferredRooms = {}
    this._roomDependencies = null
    this._delegate = dependencies.delegate
  }

  toString() {
    let serverName = this._roomDependencies ? this._roomDependencies.serverName : ''
    return `roomRegistry{serverName=${serverName}}`
  }

  getRooms() {
    return this._roomArray
  }
  getRoom(matrixId) {
    return this._roomMap[matrixId]
  }
  getOrCreateEmptyRoom(matrixId) {
    let room = this._roomMap[matrixId]
    if (room) {
      return room
    }
    return this._createRoom(matrixId)
  }
  waitForRoom(matrixId, lastState, invitedUserIds) {
    let lastStateStr = JSON.stringify(lastState)
    let invitesStr = invitedUserIds.join(',')
    log.debug(TAG, `${this} waiting for ${matrixId} lastState=${lastStateStr} invites=${invitesStr}`)

    return this._waitForRoomCreation(matrixId).then(room => {
      log.debug(TAG, `${this} got room ${matrixId}, now waiting for state`)
      let roomReady = Promise.all([
        this._waitForRoomState(room, lastState.type, lastState.stateKey),
        this._waitForRoomInvites(room, invitedUserIds),
      ])
      let readyTimeout = new Promise((resolve, reject) => {
        setTimeout(() => {
          reject(new Error(`timed out after ${ROOM_CREATE_TIMEOUT}ms while waiting for ${room} to be prepared`))
        }, ROOM_PREPARE_TIMEOUT)
      })
      return Promise.race([roomReady, readyTimeout]).then(() => room)
    })
  }
  _waitForRoomCreation(matrixId) {
    let room = this._roomMap[matrixId]
    if (room) {
      return Promise.resolve(room)
    }
    let deferred = this._deferredRooms[matrixId] = this._deferredRooms[matrixId] || []
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        reject(new Error(`timed out after ${ROOM_CREATE_TIMEOUT}ms while waiting for room, ${matrixId}`))
      }, ROOM_CREATE_TIMEOUT)
      deferred.push(resolve)
    })
  }
  _waitForRoomState(room, type, stateKey) {
    let state = room.state(type)
    log.debug(TAG, this + ' waiting for room state ' + type + '/' + (stateKey || '') + ' in ' + room)

    if (type === 'm.room.aliases') {
      // Aliases state key is the server name, which can get tricky, so we
      // just check for any alias state instead.
      if (state.keys.length) {
        return Promise.resolve()
      }

      log.debug(TAG, this + ' room alias was not set, waiting for any update in ' + room)
      return state.once('update')
    }

    if (state.has(stateKey)) {
      return Promise.resolve()
    }
    if (stateKey) {
      log.debug(TAG, this + ' state was not set, waiting for state key update in ' + room)
      return state.once('update:' + stateKey)
    } else {
      log.debug(TAG, this + ' state was not set, waiting for default value update in ' + room)
      return state.once('value')
    }
  }
  _waitForRoomInvites(room, invitedUserIds) {
    let deferred = defer()
    let waitForInvite = (invitedUserIds || []).slice()
    log.debug(TAG, `${this} waiting for invited users in ${room}: ${waitForInvite.join(',')}`)

    const consumeEvent = event => {
      if (event.type === 'm.room.member' && event.content.membership === 'invited') {
        let index = waitForInvite.indexOf(event.stateKey)
        if (index !== -1) {
          waitForInvite.splice(index, 1)
        }
      }
      if (!waitForInvite.length) {
        log.debug(TAG, `${this} has now seen all invites for invited users in ${room}`)
        room.off('event', consumeEvent)
        deferred.resolve()
      } else {
        log.debug(TAG, `${this} waiting for remaining invites in ${room}: ${waitForInvite.join(',')}`)
      }
    }

    room.on('event', consumeEvent)
    room.events.forEach(consumeEvent)

    return deferred.promise
  }

  init(roomDependencies) {
    this._roomDependencies = roomDependencies
    return this
  }
  clear() {
    for (let i in this._roomArray) {
      this._roomArray[i]._close()
    }
    this._roomArray = []
    this._roomMap = {}
    this._roomDependencies = null
  }

  _handleRoomSync(roomId, membership, roomData, isFirstSync) {
    let room = this.getOrCreateEmptyRoom(roomId)
    room._handleRoomSync(roomData, membership, isFirstSync)
  }

  _createRoom(matrixId) {
    if (this._roomMap[matrixId]) {
      argCheck.error('RoomRegistry._createRoom', 'matrixId', 'room already exists')
    }
    let room = priv(Room, {matrixId}, this._roomDependencies)
    this._roomMap[matrixId] = room
    this._roomArray.push(room)

    let deferred = this._deferredRooms[matrixId]
    if (deferred) {
      for (let i in deferred) {
        deferred[i](room)
      }
      delete this._deferredRooms[matrixId]
    }
    this._delegate._onRoomCreated(room)

    return room
  }
}



