'use strict'

import {assign, randomString} from 'common/utils'

export default function StatsFormatter(context, reporter) {
  this.context = assign({}, context)
  this.context.contextId = randomString(16)
  this.context.userAgent = navigator.userAgent
  this.reporter = reporter
  this.tag = 'stats-formatter'
}

StatsFormatter.prototype.setPeerId = function (peerId) {
  this.context.peerId = peerId
}

StatsFormatter.prototype.handleStats = function (stats) {
  for (var i in stats) {
    stats[i] = assign(stats[i], this.context)
  }
  this.reporter.handleStats(stats)
}



