'use strict'

import log from 'common/log'
import argCheck from 'common/argCheck'
import {NotAllowedError, NotFoundError} from 'common/errors'

import {getUserMedia} from 'media/common'
import ConcreteMediaSource from 'media/concreteMediaSource'

const TAG = 'firefox-screen-source'
const ERROR_TIME_BREAKPOINT_MS = 100

/**
 * Internal media node for acquiring a Firefox screen sharing stream.
 *
 * @private
 * @class FirefoxScreenSource
 * @extends ConcreteMediaSource
 * @param {Object} [options] - Optional options object.
 * @param {ScreenSharingType} [options.type='window'] - The screen sharing type to use.
 *   The difference between window and application is that application will show all application windows
 *   at once, while window only shows a single window. e.g. only a single Firefox window or all open Firefox
 *   windows.
 * @param {boolean} [options.audio=false] - Whether audio should be requested as well.
 * @param {Object} [options.video] - Constraints for the video, defaults to max resolution and no
 *   framerate limit.
 */
export default class FirefoxScreenSource extends ConcreteMediaSource {
  constructor(options) {
    super()

    const audio = options.audio || false
    const video = options.video || {}
    const type = options.type || 'window'

    argCheck.optBoolean('FirefoxScreenSource constructor', 'options.audio', audio)
    argCheck.optObject('FirefoxScreenSource constructor', 'options.video', video)
    argCheck.optString('FirefoxScreenSource constructor', 'options.type', type)

    if (!video.mediaSource) {
      video.mediaSource = type
    }
    let startTime = Date.now()
    getUserMedia({audio, video}).then(
      stream => this.setStream(stream),
      error => {
        if (error.name === 'NotAllowedError') {
          var actualError
          // If the diff is too short we can assume it's not the user...
          let timeToError = Date.now() - startTime
          if (timeToError < ERROR_TIME_BREAKPOINT_MS) {
            if (location.protocol !== 'https:') {
              actualError = new NotAllowedError('Screen sharing is only allowed over HTTPS')
            } else {
              let msg = 'Screen sharing is not allowed on this domain, it needs to be added to ' +
                'the media.getusermedia.screensharing.allowed_domains preference in about:config, ' +
                'either manually or using an extension.'
              actualError = new NotFoundError(msg)
            }
          } else {
            actualError = new NotAllowedError('The user denied the request to share the screen')
          }
          actualError.reason = error.reason
          error = actualError
        }
        this.setError(error)
      }
    ).catch(error => {
      log.error(TAG, `unexpected error in FirefoxScreenSource: ${error}`)
    })
  }
}



