'use strict'
import log from 'common/log'
import argCheck from 'common/argCheck'

import {doesntSupportPlugin} from 'media/common'
import HtmlSink from 'media/htmlSink'
import MediaNode from 'media/mediaNode'
import CanvasSource from 'media/canvasSource'
import StreamSplitter from 'media/streamSplitter'

const TAG = 'canvas-filter'

/**
 * A sink that can be used to take snapshots of a video stream that are
 * exported as either blobs or a data URIs.
 *
 * @class VideoFrameCapture
 */
export default class CanvasFilter extends MediaNode.connectMixin() {
  constructor(options) {
    super()
    doesntSupportPlugin(this)
    argCheck.optOptions('CanvasFilter constructor', 'options', options)
      .optInstance('canvas', HTMLCanvasElement)
      .optNumber('videoFrameRate')
      .optFunc('renderFunction')

    let {
      render,
      canvas,
      videoFrameRate = 24,
    } = options || {}
    if (videoFrameRate <= 0) {
      argCheck.error('CanvasFilter constructor', 'options.videoFrameRate', 'a positive number')
    }

    this._videoFrameRate = videoFrameRate
    this._renderFunction = render

    this._renderInterval = null

    this._video = document.createElement('video')

    this._splitter = new StreamSplitter()
    this._htmlSink = new HtmlSink({target: this._video})
    this._canvasSource = new CanvasSource({canvas, videoFrameRate})
    this._canvas = this._canvasSource.canvas

    this._splitter.videoOutput.connect(this._htmlSink)

    this._render = this._render.bind(this)

    this.startRender = this.startRender.bind(this)
    this.stopRender = this.stopRender.bind(this)
    this._video.addEventListener('loadeddata', this.startRender)
    this._video.addEventListener('emptied', this.stopRender)
  }

  /**
   * The input from which the video will be captured.
   *
   * @member {MediaNodeInput} CanvasFilter#input
   */
  get input() {
    return this._splitter
  }

  get output() {
    return this._canvasSource
  }

  startRender() {
    let {videoWidth, videoHeight} = this._video
    this._canvas.width = videoWidth
    this._canvas.height = videoHeight
    this._fpsInterval = 1000 / this._videoFrameRate
    this._then = Date.now()
    this._startTime = this._then
    this._render()
  }

  stopRender() {
    clearInterval(this._renderInterval)
    this._renderInterval = null
  }

  _render() {
    let now = Date.now()
    let elapsed = now - this._then
    if (elapsed > this._fpsInterval) {
      this._then = now - (elapsed % this._fpsInterval)

      try {
        this.render(this._video, this._canvas)
      } catch (error) {
        log.error(TAG, `error thrown in render function, ${error}`)
      }
    }

    requestAnimationFrame(this._render)
  }

  render(video, canvas) {
    if (this._renderFunction) {
      this._renderFunction(video, canvas)
    }
  }
}



