'use strict'
import log from 'common/log'
import EmitterMixin from 'common/emitter'
import HtmlSink from 'media/htmlSink'

const TAG = 'temasys-canvas-filter'

/**
 * A sink that can be used to make scaled down streams of existing
 * camera streams
 *
 * @class TemasysCanvasFilter
 */
export default class TemasysCanvasFilter extends EmitterMixin(null) {
  constructor(options) {
    super()
    let {
      projectionConfiguration,
      imageFrameRate = 0.5,
    } = options

    this._video = document.createElement('video')
    this._htmlSink = new HtmlSink({target: this._video})

    this._canvas = document.createElement('canvas')
    this._canvas.style.position = 'absolute'
    this._canvas.style.right = '0'

    this._canvasContainer = document.createElement('div')
    this._canvasContainer.style.zIndex = -1e10
    this._canvasContainer.style.position = 'fixed'
    this._canvasContainer.style.left = '0'
    this._canvasContainer.style.width = '0'
    this._canvasContainer.style.height = '0'
    this._canvasContainer.style.overflow = 'hidden'
    this._canvasContainer.appendChild(this._canvas)

    document.body.appendChild(this._canvasContainer)
    this._canvasContainer.appendChild(this._video)

    this._projectionConfiguration = projectionConfiguration
    this._imageFrameRate = imageFrameRate
    this._render = this._render.bind(this)
    this.startRender = this.startRender.bind(this)
    this.stopRender = this.stopRender.bind(this)
    this.render = this.render.bind(this)

    setTimeout(this.startRender, 1000) // TODO: How to start rendering properly?
  }

  startRender() {
    let {videoWidth, videoHeight} = this._htmlSink.target
    this._canvas.width = videoWidth
    this._canvas.height = videoHeight
    this._renderInterval = setInterval(this._render, 1000 / this._imageFrameRate)
  }

  stopRender() {
    clearInterval(this._renderInterval)
    this._renderInterval = null
  }

  _render() {
    try {
      this.render(this._video, this._canvas)
    } catch (error) {
      log.error(TAG, `error thrown in render function, ${error}`)
    }
  }

  _calcProjection() {
    let {videoWidth, videoHeight} = this._htmlSink.target

    let {
      width,
      aspectRatio = 16 / 9,
      contentMode = 'aspectFill',  /* scaltToFill, aspectFit or aspectFill */
    } = this._projectionConfiguration

    let height = width / aspectRatio
    this._canvas.width = width
    this._canvas.height = height

    let videoAspectRatio = videoWidth / videoHeight

    if (contentMode === 'aspectFill') {
      if (videoAspectRatio < aspectRatio) {
        let delta = width / videoAspectRatio - height

        return {
          width: width,
          height: height + delta,
          deltaY: -delta / 2,
          deltaX: 0,
        }
      } else {
        let delta = height * videoAspectRatio - width

        return {
          height: height,
          width: width + delta,
          deltaY: 0,
          deltaX: -delta / 2,
        }
      }
    }

    if (contentMode === 'scaleToFill') {
      return {
        height: height,
        width: width,
        deltaY: 0,
        deltaX: 0,
      }
    }
  }

  render(video, canvas) {
    let {videoWidth, videoHeight} = this._htmlSink.target
    if (!this._projection) {
      this._projection = this._calcProjection()
    }

    let base64img = this._htmlSink.target.getFrame()
    let image = new Image()
    image.onload = () => {
      let ctx = this._canvas.getContext('2d')
      ctx.drawImage(image, 0, 0, videoWidth, videoHeight,
                    this._projection.deltaX, this._projection.deltaY,
                    this._projection.width, this._projection.height)

      let dataUrl = this._canvas.toDataURL('image/jpeg')
      this._emit('stillPictureCaptured', dataUrl)
    }

    image.setAttribute('src', 'data:image/png;base64,' + base64img)
  }

  get input() {
    return this._htmlSink
  }
}



