'use strict'

import log from 'common/log'
import {defer, randomString} from 'common/utils'

import {OpEntry, ConfigurationEntry} from 'conference/raftLogEntry'
import {StateOpRequest, StateOpResponse} from 'conference/raftMessages'
import {EXPECTED_RTT_MS} from 'conference/raftState'

const TAG = 'raft'

const TICK_INTERVAL = 100
const TICK_APPLIED_FACTOR = 10
const STATE_OP_APPLY_TTL_MS = 30 * 1000
const RESEND_RTT_FACTOR = 6

// The StateMachine keeps the local state of the Raft cluster, and applies
// committed entries to that state after deduplication. It is also responsible
// for keeping track of operations that have been requested by the local
// client, and to retry them if needed.
//
// This diagram describes the flow of a successful state operation where the
// local client is the leader.
//
//      Raft --3-> LeaderState --4b-> ...
//      |  ^            |
//      1  2            4a
//      v  |            v
// StateMachine <-5-- RaftLog
//
// 1. An operation is requested.
// 2. The state machine sends a request via the onStateOpRequest callback.
// 3. Raft forwards the request to the current state.
// 4a. Append the OpEntry to the log.
// 4b. Raft algorithm in action, which ends up committing the entry.
// 5. The log forwards committed entries to the state machine.
//
// And now a diagram for if we're a follower.
//
//      Raft --3-> FollowerState --4-> LeaderState --5a->
//      |  ^                                |
//      1  2                                5a
//      v  |                                v
// StateMachine <-7-- RaftLog <- - 6 - - RaftLog
//
// 1-3. Same as above.
// 4. Send StateOpRequest to the current leader.
// 5a 5b. Same as 4a 4b above.
// 6. Raft algorithm ends up committing the entry in the follower's log.
// 7. Same as 5 above.
//
export default class StateMachine {
  constructor({onStateChange, onStateOpRequest, onConfigurationCommitted}) {
    this._tick = this._tick.bind(this)

    this._state = {}
    this._tickMod = 0

    // Client requests are tracked here, type is Map<opId, [expiryMs, deferred, StateOpRequest]>
    this._tuples = new Map()

    // All members keep track of all op entry IDs that have been applied.
    this._appliedIds = new Map()
    this._onStateChange = onStateChange
    this._onStateOpRequest = onStateOpRequest
    this._onConfigurationCommitted = onConfigurationCommitted
  }

  toString() {
    return `stateMachine{${JSON.stringify(this.state)}}`
  }

  get state() {
    return this._state
  }

  start() {
    this.stop()
    this._tickMod = 0
    this._intervalId = setInterval(this._tick, TICK_INTERVAL)
  }

  stop() {
    this.clear()
    if (this._intervalId) {
      clearInterval(this._intervalId)
      this._intervalId = null
    }
  }

  clear() {
    this._state = {}
    this._tuples.clear()
    this._appliedIds.clear()
  }

  requestStateOp({op, args, retryTtlMs}) {
    let id = randomString(OpEntry.idSize)
    let request = StateOpRequest.create({id, op, args})
    let now = Date.now()
    let expiry = now + retryTtlMs
    let nextResend = now + EXPECTED_RTT_MS * RESEND_RTT_FACTOR
    let deferred = defer()
    this._tuples.set(id, {expiry, nextResend, deferred, request})
    this._onStateOpRequest(request)
    return deferred.promise
  }

  handleStateOpResponse(peerId, message) {
    let {id, errorCode} = message
    let tuple = this._tuples.get()
    if (!tuple) {
      log.info(TAG, this, ` got StateOpResponse for unknown tuple, ${id}`)
      return
    }
    if (errorCode) {
      if (errorCode === StateOpResponse.NOT_LEADER) {
        this._onStateOpRequest(tuple.request)
        tuple.nextResend = Date.now() + EXPECTED_RTT_MS * RESEND_RTT_FACTOR
      } else {
        log.error(TAG, this, ` got unknown StateOpResponse errorCode for ${id}, ${errorCode}`)
      }
    } else {
      // Success, reset resend timeout
      tuple.nextResend = Date.now() + EXPECTED_RTT_MS * RESEND_RTT_FACTOR
    }
  }

  _tick() {
    let now = Date.now()
    // Applied ids don't need to be checked nearly as often as tuple resends, so
    // only do it every nth tick.
    this._tickMod = (this._tickMod + 1) % TICK_APPLIED_FACTOR
    if (this._tickMod === 0) {
      this._appliedIds.forEach((expiry, entryId) => {
        if (expiry < now) {
          this._appliedIds.delete(entryId)
        }
      })
    }
    this._tuples.forEach((tuple, entryId) => {
      if (tuple.expiry < now) {
        this._tuples.delete(entryId)
        tuple.deferred.reject(new Error('Timed out'))
      } else if (tuple.nextResend < now) {
        this._onStateOpRequest(tuple.request)
        tuple.nextResend = now + EXPECTED_RTT_MS * RESEND_RTT_FACTOR
      }
    })
  }

  commitEntries(entries) {
    let updated = false
    entries.forEach(entry => {
      if (entry instanceof OpEntry) {
        if (this._appliedIds.has(entry.id)) {
          log.debug(TAG, this, `ignored duplicate entry, ${entry}`)
        } else {
          let expiry = Date.now() + STATE_OP_APPLY_TTL_MS
          if (entry.id) {
            this._appliedIds.set(entry.id, expiry)
          }
          let newState = entry.apply(this._state)
          if (newState) {
            updated = true
            this._state = newState
          }
        }
        let tuple = this._tuples.get(entry.id)
        if (tuple) {
          tuple.deferred.resolve()
          this._tuples.delete(entry.id)
        }
      } else if (entry instanceof ConfigurationEntry) {
        this._onConfigurationCommitted && this._onConfigurationCommitted(entry.data)
      }
    })
    if (updated) {
      this._onStateChange && this._onStateChange()
    }
  }

  revertEntries(entries) {
    entries.forEach(entry => {
      let tuple = this._tuples.get(entry.id)
      if (tuple) {
        this._onStateOpRequest(tuple.request)
        tuple.nextResend = Date.now() + EXPECTED_RTT_MS * RESEND_RTT_FACTOR
      }
    })
  }

  loadSnapshot(snapshot) {
    this._state = snapshot.state

    this._onStateChange && this._onStateChange()
    this._onConfigurationCommitted && this._onConfigurationCommitted(snapshot.configuration)
  }
}



