'use strict'

import argCheck from 'common/argCheck'

const TYPE_ROOM = 'room'
const TYPE_USER = 'user'
const TYPE_EVENT = 'event'
const TYPE_ALIAS = 'alias'

const tagTypeMap = {
  '!': TYPE_ROOM,
  '@': TYPE_USER,
  '$': TYPE_EVENT,
  '#': TYPE_ALIAS,
}
const typeTagMap = {
  [TYPE_ROOM]: '!',
  [TYPE_USER]: '@',
  [TYPE_EVENT]: '$',
  [TYPE_ALIAS]: '#',
}

export function parseId(matrixId) {
  argCheck.string('parseId', 'matrixId', matrixId)
  let [id, ...rest] = matrixId.split(':')
  let typeTag = id[0]
  let localId = id.slice(1)
  if (localId.length === 0) {
    argCheck.error('parseId', 'matrixId', 'not have an empty local id')
  }
  let type = tagTypeMap[typeTag]
  if (!type) {
    argCheck.error('parseId', 'matrixId', 'have a valid type tag')
  }
  let serverName = rest.join(':')
  if (serverName.length === 0) {
    argCheck.error('parseId', 'matrixId', 'not have an empty server name')
  }
  return [type, localId, serverName]
}

export function parseIdForServer(context, matrixId, serverName, expectedType) {
  argCheck.string(context, 'serverName', serverName)
  let [type, localId, parsedServerName] = parseId(matrixId)
  if (type !== expectedType) {
    argCheck.error(context, 'type', `be ${expectedType} and not ${type}`)
  }
  if (serverName === parsedServerName) {
    return localId
  } else {
    return `${localId}:${parsedServerName}`
  }
}

export function toMatrixIdForServer(context, id, serverName, type) {
  argCheck.string(context, 'id', id)
  let tag = typeTagMap[type]
  if (!type) {
    argCheck.error(context, 'type', 'use a valid type')
  }

  if (id.indexOf(':') === -1) {
    argCheck.string(context, 'serverName', serverName)
    id = `${id}:${serverName}`
  }
  if (id[0] !== tag) {
    id = tag + id
  }

  return id
}



