'use strict'

import argCheck from 'common/argCheck'

import {doesntSupportPlugin} from 'media/common'
import MediaSource from 'media/mediaSource'

export default class CanvasSource extends MediaSource {
  constructor(options) {
    super()
    doesntSupportPlugin(this)
    argCheck.options('CanvasSource constructor', 'options', options)
      .optInstance('canvas', HTMLCanvasElement)
      .optNumber('videoFrameRate')
    let {canvas, videoFrameRate = 24} = options

    if (canvas) {
      this._canvas = canvas
    } else {
      this._canvas = document.createElement('canvas')
      this._canvas.style.position = 'absolute'
      this._canvas.style.right = '0'

      this._canvasContainer = document.createElement('div')
      this._canvasContainer.style.zIndex = -1e10
      this._canvasContainer.style.position = 'fixed'
      this._canvasContainer.style.left = '0'
      this._canvasContainer.style.width = '0'
      this._canvasContainer.style.height = '0'
      this._canvasContainer.style.overflow = 'hidden'
      this._canvasContainer.appendChild(this._canvas)

      document.body.appendChild(this._canvasContainer)
    }

    this._canvas.getContext('2d') /* initialize context or the below line will fail in Firefox */
    this.setStream(this._canvas.captureStream(videoFrameRate))
  }

  stop() {
    if (this._canvasContainer) {
      document.body.removeChild(this._canvasContainer)
      this._canvasContainer = null
    }
    this._canvas = null
    this.stream.stop()
  }

  get canvas() {
    return this._canvas
  }
}



